import React, { Component } from 'react';
import { Dropdown, DropdownButton } from 'react-bootstrap';
import jwtDecode from 'jwt-decode'
import history from '../history'
import { Link } from 'react-router-dom';

// ... REDUX ...
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";

class AccountDropdown extends Component {

    constructor(props)
    {
        super(props);

        this.state={
            reload:null
        }
    }

    static getDerivedStateFromProps(props, state) {
        console.log('Props :: ' + props.user);
        if(props.reload != null && props.reload != state.reload)
        {
            // props.removeUserData(props.user)
            return (
                props.onLogoutClicked()
            )
        }
    }

    handlePofile = () => {
        history.push({
            pathname:'/myaccount/myprofile'
        })
    }   
    handleCashCoupons = () => {
        history.push({
            pathname:'/myaccount/cashcoupons'
        })
    }   

    handleShortlist = () => {
        history.push({
            pathname:'/myaccount/myshortlist'
        })
    }

    handleHistory = () => {
        history.push({
            pathname:'/myaccount/orderhistory' 
        })
    }

    handleLogout = () => {
        this.props.Logout();
        this.props.removeUserData();
    }

    render() {

        let token = localStorage.getItem('jwtToken')
        let user = null;
        if(token)
        {
            user = jwtDecode(localStorage.getItem('jwtToken'));
        }
        
        return (
            <> 
            {user && (
                <DropdownButton id="account-dropdown" title={`${user.cust_name}'s Account`}>
                    <Dropdown.Item onClick={this.handleCashCoupons}>Cash in My Account</Dropdown.Item>
                    <Dropdown.Item onClick={this.handleHistory}>Order History</Dropdown.Item>
                    {/* <Dropdown.Item href="#/action-3">Quick Reorder</Dropdown.Item> */}
                    <Dropdown.Item onClick={this.handlePofile}>My Profile</Dropdown.Item>
                    {/* <Dropdown.Item href="#/action-3">My Reviews & Uploads</Dropdown.Item> */}
                    {/* <Dropdown.Item href="#/action-3">Notify Me</Dropdown.Item> */}
                    <Dropdown.Item onClick={this.handleShortlist}>My Shortlist</Dropdown.Item>
                    <Dropdown.Item onClick={this.handleLogout}>Logout</Dropdown.Item>
                    <Dropdown.Item href="#/action-3"></Dropdown.Item>
            </DropdownButton> )}
            </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    console.log('State :: ' + JSON.stringify(state.accounts.auth));
    return {
        reload: state.accounts.auth.reload
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        Logout: Actions.Logout,
        removeUserData: Actions.removeUserData
    }, dispatch)
}

export default withReducer('accounts', reducer)
                (
                    connect(mapStateToProps, mapDispatchToProps)(AccountDropdown)
                )

