import React, { Component } from 'react'
import { Row, Col, Button } from 'react-bootstrap';
import ShortlistProduct from './ShortlistProduct';
import Product from "../../product.json";
import Cookies from 'universal-cookie';

import CartPopup from '../CartPopup';

import {
    getLocalStorageObject,
    getLocalStorageString
} from '../Utilities'

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";

class MyShortlist extends Component {
    constructor(props) {
        super(props);
        this.state = {
          data: [],
          procache: [],
          cookie: [],
          selectAll: false,
          selectedShortlistId: [],
          cartToggle: false,
          shortlist:false
        };
      }

      componentDidMount() {
        this.saveData();
      }

      handleClickId = (value, checked) => {
        // console.log("check " + checked)
        let { selectedShortlistId } = this.state;
        if (checked) {
            selectedShortlistId.push(value);
        } else {
            selectedShortlistId.splice(selectedShortlistId.indexOf(value),1);
        }
        this.setState({ selectedShortlistId });
        // console.log("id selected " + this.state.selectedShortlistId)
    }
     
    handleShortlistStatus = () => {
        this.setState({
            shortlist: !this.state.shortlist
        })
    }

    handleShowCart = (product, pro_qty) => {
        this.props.saveDataToCart(product,pro_qty);
        this.setState({ cartToggle: true });
    }

    handleClose = () => {
        this.setState({ cartToggle: false });
    }
    
      saveData = () => {
        const cookies = new Cookies();
        return this.setState({
          procache: getLocalStorageObject('procache'),
          data: getLocalStorageObject('shortlist'),
          cookie: cookies.get('shortlist')
        });
      };
    render() {
        const cookies = new Cookies();
        const currentCookies = getLocalStorageObject('shortlist');
        let currentCache = getLocalStorageObject('procache');
        return (
            <>
                <div className="main-account profile">
                    <h3>My Shortlist <span>(1  Products)</span></h3>
                    <div className="btn-selection">
                        <Row>
                            {/* <Col md={{span: 8, offset: 2 }} className="d-flex justify-content-center align-items-center">
                                    <Button size="sm" className="btn btn-buy">
                                        BUY SELECTED
                                    </Button>
                                    <Button size="sm" className=" btn btn-remove">
                                        REMOVE SELECTED
                                    </Button>
                            </Col> */}
                            <Col md={2}>
                                <span
                                     onClick={() => {
                                        localStorage.removeItem('shortlist');
                                        this.handleShortlistStatus();
                                        }
                                     }
                                      className="select-clear"
                                >clear all</span>
                            </Col>
                        </Row>
                    </div>
                    <Row>
                    {currentCookies &&
                      Object.keys(currentCookies).length >= 1 ?
                      currentCookies.map(item =>
                            <ShortlistProduct product={item} addToCartAction={this.handleShowCart} saveProductToShortlist={this.props.saveProductToShortlist} 
                            handleShortlistStatus={this.handleShortlistStatus}/>
                          ) : 
                          <h4>Sorry! there is no product in your shortlist</h4>}
                    </Row>
                    

                    <div className="btn-selection top">
                        {/* <Row>
                            <Col md={{span: 8, offset: 2 }} className="d-flex justify-content-center align-items-center">
                                    <Button size="sm" className="btn btn-buy">
                                        BUY SELECTED
                                    </Button>
                                    <Button size="sm" className=" btn btn-remove">
                                        REMOVE SELECTED
                                    </Button>
                            </Col>
                            <Col md={2}>
                                <span
                                     onClick={() => {
                                        this.setState({ selectAll: !this.state.selectAll });
                                      }}
                                      className="select-clear"
                                >{this.state.selectAll === true ? "Clear All" : "Select All"}</span>
                            </Col>
                        </Row> */}
                    </div>
                </div>
                <CartPopup  handleClose={this.handleClose} show={this.state.cartToggle}/>
            </>
        )
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        saveDataToCart: Actions.saveDataToCart,
        saveProductToShortlist: Actions.saveProductToShortlist
    }, dispatch)
}


export default withReducer('shortlist', reducer)
            (
                connect(null, mapDispatchToProps)(MyShortlist)
            )

