import React, { Component } from "react";
import { useMediaQuery } from 'react-responsive';
import { Modal, Button, Table, Tabs, Tab } from "react-bootstrap";
import Product from "./../product.json";
import QtnCount from "../compontents/QtnCount";
import Cookies from 'universal-cookie';

import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";

import {removeProductFromCache, removeProductFromshortlist, getLocalStorageObject } from './Utilities';

import CartDetails from './CartDetails';
import { hostUrl } from "./store/config.js";

const Desktop = ({ children }) => {
  const isDesktop = useMediaQuery({ minWidth: 601 })
  return isDesktop ? children : null
}

const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({ maxWidth: 600 })
  return isMobile ? children : null
}

class CartPopup extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      shortlist: false,
      value: 1,
      data: [],
      zip: false,
      procache: [],
      total: 0,
      summary: { 
        totalValue: 0, 
        discount: 0, 
        shipping_charges: 0, 
        estimated_gst: 0, 
        final_payment: 0
      }
    };
  }

  componentDidMount() {
    // this.saveData();
    // this.props.shipping &&  console.log('Shipping TEST :: ' + JSON.stringify(this.props.shipping));
    this.props.getCartSummary(this.props.coupon_details ? this.props.coupon_details : {}, this.props.shipping && this.props.shipping);

  }

  static getDerivedStateFromProps(props, state) {
    // props.couponMsg &&  console.log('MSG of coupon :: ' + props.couponMsg);
    // console.log('Shipping in Pop :: ' + props.shipping && JSON.stringify(props.shipping));
    return {
      itemCount: props.itemCount,
      cart_summary: props.cart_summary,
      data: props.data,
      show: props.show,
      coupon_applied : props.coupon_applied,
      couponMsg: props.couponMsg,
      coupon_details: props.coupon_details,
      shipping: props.shipping
    }
  }

  componentDidUpdate(prevProps, prevState) {
      if(prevProps.show !== this.props.show)
      {
        // alert('Calling Get Carrt Summary')
        this.props.getCartSummary(this.props.coupon_details ? this.props.coupon_details : {}, this.props.shipping && this.props.shipping);
        this.setState({
          show: this.props.show
        })
      }
  }

  decrease = () => {
    if (this.state.value) {
      this.setState({
        value: this.state.value - 1
      });
    }
  };

  increase = () => {
    this.setState({ value: this.state.value + 1 });
  };

  findProductById = (data, idToLookFor) => {
    for (var i = 0; i < Object.keys(data).length; i++) {
      if (data[i].Id === parseInt(idToLookFor)) {
        return data[i];
      }
    }
  };

  handleValue = (value) => {
    this.setState({value: value});
  };

  handleTotal = (total) => {
    this.setState({total: total});
  };

  handleShortlist = () => {

    this.setState({
      shortlist: !this.state.shortlist
    })
  }


  render() {
    const cookies = new Cookies();
    const currentCookies = getLocalStorageObject('shortlist');
    let currentCache = JSON.parse(localStorage.getItem('procache'));
    let currentDetail = JSON.parse(localStorage.getItem('cachedProDetail'));
    let { itemCount } = this.state;

    return (
      <>
        <Modal
          className="cust-modal cart-modal"
          show={this.props.show}
          onHide={this.props.handleClose}
          size="lg"
        >
          <div className="btn-close">
            <Button
              type="button" 
              className="close"
              onClick={this.props.handleClose}
            >
              <span aria-hidden="true">×</span>
              <span className="sr-only">Close</span>
            </Button>
          </div>

          <Modal.Body>
            <Tabs defaultActiveKey="cart">
              <Tab eventKey="cart" title={<><Mobile>MY CART ({itemCount} Products)</Mobile><Desktop>MY SHOPPING CART ({itemCount}) items)</Desktop></>}>
               
                <CartDetails 
                  handleShortlist={this.handleShortlist} 
                  currentCache={currentCache} 
                  currentDetail={currentDetail} 
                  handleValue={this.handleValue} 
                  handleTotal={this.handleTotal} 
                  saveData={this.props.getCartSummary} 
                  zip={this.state.zip} 
                  totalValue={this.state.cart_summary && this.state.cart_summary.totalValue} 
                  discount={this.state.cart_summary && this.state.cart_summary.discount} 
                  shipping_charges={this.state.cart_summary && this.state.cart_summary.shipping_charges} 
                  remaining_amount={this.state.cart_summary && this.state.cart_summary.remaining_amount}
                  final_payment={this.state.cart_summary && this.state.cart_summary.final_payment} 
                  data={this.state.data}
                  applyCoupon={this.props.applyCoupon}
                  coupon_applied={ this.state.coupon_applied && this.state.coupon_applied}
                  couponMsg={this.state.couponMsg && this.state.couponMsg}
                  coupon_details= {this.state.coupon_details && this.state.coupon_details}
                  handleClose={this.props.handleClose}
                />
              </Tab>
              <Tab eventKey="shortlist" title="MY SHORTLIST">
                <Desktop>
                <Table
                  bordered
                  responsive
                  className="cart-pro shortlist-pro table-fixed"
                >
                  <thead>
                    <tr>
                      <th>Product</th>
                      <th>Price</th>
                      <th>Quantity</th>
                      <th>Total</th>
                      <th />
                    </tr>
                  </thead>
                  <tbody>
                  {currentCookies &&
                      Object.keys(currentCookies).length >= 1 &&
                      currentCookies.map(item =>
                          (
                            <tr>
                              <td>
                                <div class="pro-content">
                                  <img
                                    src={`${hostUrl}${(item.Image.split(','))[0]}`}
                                    alt=""
                                  />
                                  <div>
                                    <a href="#a" className="pro-name">
                                      {item.Name}
                                      {/* Babyhug Full Length Lycra Leggings - Red */}
                                    </a>
                                    <p className="pro-desc">
                                      {(item['Description']).replace(/(<([^>]+)>)/ig,"").substring(0, 60)}
                                    </p>
                                  </div>
                                </div>
                              </td>
                              <td className="price-td">
                                {item['Sale Price'] ? 
                                  <>
                                    <p class="pro-o-price">Rs {item.Price}</p>
                                    <p className="pro-price">{item['Sale Price']}</p>
                                    <p className="pro-disc">{Math.round(((item['Price'] - item['Sale Price']) / item['Price'] * 100))}% OFF</p>
                                    <p className="pro-disc-price">Discount Rs {item['Price'] - item['Sale Price']}</p>
                                    <p class="pro-tax">MRP incl. all taxes</p>
                                  </>:
                                    <p className="pro-price">{item['Price']}</p>
                                }
                              </td>
                              <td className="quantity-td">
                                <div className="qtn-inc-dec">
                                  <div>
                                    <button onClick={this.decrease} className="minus icon-minus rm-qty" disabled>
                                      
                                    </button>
                                    <input
                                      className="quantity"
                                      min="1"
                                      name="quantity"
                                      value={this.state.value}
                                      // onChange={() => console.log("change")}
                                      type="text"
                                    />
                                    <button onClick={this.increase} className="plus icon-plus"  disabled>
                                      
                                    </button>
                                  </div>
                                </div>
                                <div className="qtn-action">
                                  <div 
                                    className="cart-remove"
                                    onClick={() => {
                                      removeProductFromshortlist(item.Id);
                                      this.props.getCartSummary(this.state.coupon_details && this.state.coupon_details, this.state.shipping && this.state.shipping);
                                    }}
                                  />
                                </div>
                              </td>
                              <td className="total-td">
                                {item['Sale Price'] ? 
                                <p className="pro-total">{item['Sale Price']}</p>:
                                <p className="pro-total">{item.Price}</p>
                                  }
                              </td>
                              <td>
                                <a class="btn-cart" href="#a">
                                  PLACE ORDER
                                </a>
                              </td>
                            </tr>
                      )
                        )}
                  </tbody>
                </Table>
                </Desktop>
                <Mobile>
                  <div className="mobile-shortlist">
                  {currentCookies &&
                      Object.keys(currentCookies).length >= 1 &&
                      currentCookies.map(item =>
                          (
                            <div className="shortlist-item">
                                <div className="pro-img">
                                  <img
                                    src={`${hostUrl}${(item.Image.split(','))[0]}`}
                                    alt=""
                                  />
                                </div>
                                <div className="pro-detail">
                                  <div className="pro-name"> {item.Name}</div>
                                  <div className="pro-price-content">
                                    {item['Sale Price'] ? 
                                      <>
                                        <p class="pro-o-price">Rs {item.Price}</p>
                                        <p className="pro-price">{item['Sale Price']}</p>
                                        <p className="pro-disc">{Math.round(((item['Price'] - item['Sale Price']) / item['Price'] * 100))}% OFF</p>
                                      </>:
                                        <p className="pro-price">{item['Price']}</p>
                                    }
                                  </div>
                                  <div className="qtn-action">
                                    <div className="add-to-cart">
                                      Add to Cart
                                    </div>
                                    <div 
                                      className="cart-remove"
                                      data-icon-font="#"
                                      onClick={() => {
                                        removeProductFromshortlist(item.Id);
                                        this.props.getCartSummary(this.state.coupon_details && this.state.coupon_details, this.state.shipping && this.state.shipping);
                                      }}
                                    />
                                  </div>
                                </div>
                            </div>
                       )
                       )}
                  </div>
                </Mobile>
              </Tab>
            </Tabs>
          </Modal.Body>
        </Modal>
      </>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    itemCount: Math.max (state.cartReducer.cart.itemCount ? state.cartReducer.cart.itemCount : 0 , state.cartReducer.auth.itemCount ? state.cartReducer.auth.itemCount : 0),
    cart_summary: state.cartReducer.cart.cart_summary,
    data: state.cartReducer.cart.data,
    coupon_applied: state.cartReducer.order.coupon_applied,
    couponMsg: state.cartReducer.order.couponMsg,
    coupon_details: state.cartReducer.order.coupon_details,
    shipping: state.cartReducer.shipping.shipping
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
    getCartSummary: Actions.getCartSummary,
    saveDataToCart: Actions.saveDataToCart,
    updateCart: Actions.updateCart,
    applyCoupon: Actions.applyCoupon
  }, dispatch)
}

export default withReducer('cartReducer',reducer)
            (connect( mapStateToProps, mapDispatchToProps)(CartPopup));