import React from "react";
import {
	Navbar,
	Nav,
	Form
} from "react-bootstrap";
import Login from "./LoginPopup";
import Register from "./RegisterPopup";
import Headroom from "react-headroom";
import MobileHeader from "./mobile/MobileHeader";
import AccountDropdown from "./AccountDropdown";
import CategoryNavItem from "./CategoryNavItem";
import Search from "./Search"

import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";
import TopNotification from './TopNotification'
import history from '../history'
import { NavLink, Link } from 'react-router-dom'

import { LinkContainer } from 'react-router-bootstrap'
import MiniCart from './MiniCart'
import { getLocalStorageObject } from './Utilities'

class MainHeader extends React.Component {  
	constructor(props) {
		super(props);
		this.state = {
			showLogin: false,
			showRegister: false,
			categoriesData: [],
			categoriesTags: [],
			reRender: 1,
			reload:false,
			activeNav: false,
			isClicked:false
		};
	}

	componentDidMount() {
		this.props.getCategories();   
		this.props.getShipping();
	}

	static getDerivedStateFromProps(props, state) {
		return {
			reRender: props.reRender,
			categoriesData: props.categoriesData,
			categoriesTags: props.categoriesTags,
			attributes: props.attributes
		}
	}

	handleCloseRegister = () => {
		this.setState({ showRegister: false });
	};

	handleShowRegister = () => {
		this.setState({ showRegister: true });
	};

	handleCloseLogin = () => {
		this.setState({ showLogin: false });
	};

	handleShowLogin = () => {
		this.setState({ showLogin: true });
	};

	handlePop = trigger => {
		this.setState({
			showLogin:false,
			showRegister: false
		})

		this.props.renderAction();
	}

	handleLogout = event => {
		this.setState({
			reload: event
		})
		this.props.renderAction();
	}

	handleActiveNav = () => {
		this.setState({
			activeNav : !this.state.activeNav
		})
	}

	handleDropDown = (status) => {
		this.setState({
			isClicked: status
		})
	}

	render() {
		let productsInCart = Object.keys(getLocalStorageObject('procache')).length;
		// const { categoriesData } = this.state;
		let token = localStorage.getItem('jwtToken');

		var md;

		if(this.state.categoriesData) var itemCount = Object.keys(this.state.categoriesData).length;
		if (itemCount >= 11 && itemCount <= 22) {
			md = 4;
		} else if (itemCount >= 23) {
			md = 6;
		} else {
			md = 3;
		}

		return (
			<div>
				<TopNotification />
				<MobileHeader categoryData={this.state.categoriesData}/>
				{/* <div style={{display: this.state.activeNav === true ? "block" : "none"}} className="body-bg"></div> */}
				<div className="desktop-header">
					<div>
						<div className="topbar">
							{/* <span>
								<a href="#a">Become a Franchisee </a>
							</span> */}
							<span>
								<a href="#a" rel="nofollow">
									Contact Us
								</a>
							</span>
							<span>
								<a href="#a" rel="nofollow">
									Babynest Parenting
								</a>
							</span>
							<span>
								<Link to="/track">
									Track Order
								</Link>
							</span>

							{token ? <AccountDropdown onLogoutClicked={this.handleLogout}/> : <>
							<span
								onClick={() => {
									this.handleShowRegister();
								}}
							>
								Register
							</span>
							<span
								onClick={() => {
									this.handleShowLogin();
								}}
							>
								Login
							</span> </>}
						</div>
					</div>
					<header>
						<div className="header-container">
							<LinkContainer to="/">
							<Navbar.Brand >
								<img src="/assets/images/logo.jpg" alt="Logo" />
							</Navbar.Brand>
							</LinkContainer>
							<Form inline className="prosearch">
								{/* <FormControl
									type="text"
									placeholder="Search for a Category, Brand or Product"
									className="mr-sm-2"
								/> */}

								<Search />

								<img
									className="search-button"
									src="/assets/images/search-icon.png"
									alt="search"
								/>
							</Form>
							<MiniCart itemCount = {productsInCart} />
						</div>
					</header>
					<Headroom
						style={{
							WebkitTransition: "all .5s ease-in-out",
							MozTransition: "all .5s ease-in-out",
							OTransition: "all .5s ease-in-out",
							transition: "all .5s ease-in-out"
						}}
					>
						<Navbar sticky="top" className={this.state.isClicked ? "navbar-bg activeclick" : "navbar-bg"} bg="light" expand="lg">
							<Navbar.Toggle aria-controls="basic-navbar-nav" />
							<Navbar.Collapse id="basic-navbar-nav">
								<Nav className="mr-auto"
									// onMouseEnter={() => {
									//   if (this.state.activeNav !== true)
									//     this.setState({ activeNav: true });
									// }}
									// onMouseLeave={() => {
									//   if (this.state.activeNav !== false)
									//     this.setState({ activeNav: false });
									// }}
								>
									{this.state.categoriesData && this.state.categoriesData.map(category => {
										// console.log (JSON.stringify(category));
										return (
											// <Nav.Link as={Link} to={`/parent_cat/${category.child_slug}`}>
												<CategoryNavItem
													key={category.child_id}
													category={category}
													tags={ category.AllTags }
													subCategories={category.subCategories}
													md={md}
													handleNav={this.handleActiveNav}
													handleDropDown={this.handleDropDown}
													attributes={this.state.attributes && this.state.attributes}
													// as={NavLink} to={`/parent_cat/${category.child_slug}`}
												/>
											// </Nav.Link>
										);
									})}
								</Nav>
							</Navbar.Collapse>
						</Navbar>
					</Headroom>
					<Login
						{...this.props}
						handleShowRegister={this.handleShowRegister}
						handleShowLogin={this.handleShowLogin}
						id={this.state.id}
						handleCloseLogin={this.handleCloseLogin}
						show={this.state.showLogin}
						onPopClicked={this.handlePop}
					/>
					<Register
						{...this.props}
						handleShowLogin={this.handleShowLogin}
						handleShowRegister={this.handleShowRegister}
						id={this.state.id}
						handleCloseRegister={this.handleCloseRegister}
						show={this.state.showRegister}
						onPopClicked={this.handlePop}
					/>
				</div>
			</div>
		);
	}
}

function mapStateToProps(state)
{
	// console.log('Categories Data :: ' + state.headers.header.categoriesData);
		return {
				reRender: state.headers.header.reRender,
				categoriesData: state.headers.header.categoriesData,
				categoriesTags: state.headers.header.categoriesTags, 
				attributes: state.headers.header.attributes
		}
}

const mapDispatchToProps = (dispatch, ownProps) => {
	return bindActionCreators({
		renderAction: Actions.renderAction,
		getCategories: Actions.getCategories,
		getShipping: Actions.getShipping,
	}, dispatch)
}

export default withReducer('headers',reducer)(connect(mapStateToProps, mapDispatchToProps)(MainHeader));

