const AsyncHome = lazy(() => import("./compontents/home/Home"));
const AsyncCategoryPage = lazy(() => import("./compontents/CategoryPage"));
const AsyncParentCategoryPage = lazy(() =>
  import("./compontents/CategoryParentPage")
);
const AsyncQuickView = lazy(() => import("./compontents/QuickView"));
const AsyncProductDetailsPage = lazy(() =>
  import("./compontents/product/ProductDetailsPage")
);
//const AsyncAccountPage = lazy(() => import("./compontents/account/AccountPage"));
const AsyncNotFoundPage = lazy(() => import("./compontents/NotFoundPage"));
const AsyncMainHeader = lazy(() => import("./compontents/MainHeader"));
const AsyncFooter = lazy(() => import("./compontents/Footer"));
const AsyncLoginPage = lazy(() => import("./compontents/LoginPage"));
const AsyncTrackOrderPage = lazy(() => import("./compontents/TrackOrderPage"));
const AsyncCheckoutPage = lazy(() => import("./compontents/CheckoutPage"));
const AsyncMobileSearchPage = asyncComponent(() =>
  import("./compontents/mobile/MobileSearch")
);
const AsyncContactPage = asyncComponent(() =>
  import("./compontents/contactPage/contact")
);
const AsyncAboutPage = asyncComponent(() =>
  import("./compontents/aboutUs/aboutUs")
);
const AsyncPrivacyPage = asyncComponent(() =>
  import("./compontents/privacyPolicy/privacyPolicy")
);
const AsyncShippingPolicyPage = asyncComponent(() =>
  import("./compontents/shippingPolicy/shippingPolicy")
);
const AsyncTermsPage = asyncComponent(() =>
  import("./compontents/terms/terms")
);

//payment webview

const AsyncPaymentWebview = asyncComponent(() =>
  import("./compontents/webviewPayments/webviewPayments")
);

class App extends Component {
  render() {
    return (
      <div>
        <AsyncMainHeader />
        <Suspense fallback={<Loading />}>
          <Switch>
            <Route exact path="/" component={AsyncHome} />
            <Route
              exact
              path="/categories/:id/:tag/:p_cat/:slug"
              component={AsyncCategoryPage}
            />
            <Route
              exact
              path="/parent_cat/:id/:p_cat/:slug"
              component={AsyncParentCategoryPage}
            />
            <Route exact path="/quickview/:id" component={AsyncQuickView} />
            <Route
              exact
              path="/brand/:brand"
              component={AsyncProductDetailsPage}
            />
            <Route
              exact
              path="/product/:slug"
              component={AsyncProductDetailsPage}
            />
            <Route exact path="/login" component={AsyncLoginPage} />
            <Route exact path="/track" component={AsyncTrackOrderPage} />
            <Route path="/myaccount" component={AccountPage} />
            <Route path="/checkout" component={AsyncCheckoutPage} />
            <Route path="/search" component={AsyncMobileSearchPage} />
            <Route path="/contactUs" component={AsyncContactPage} />
            <Route path="/aboutUs" component={AsyncAboutPage} />
            <Route path="/privacy" component={AsyncPrivacyPage} />
            <Route
              path="/shipping-policy"
              component={AsyncShippingPolicyPage}
            />
            <Route path="/terms" component={AsyncTermsPage} />
            <Route path="/webViewPayment" component={AsyncPaymentWebview} />
            {/* <Route path='/Loading' component={Loading} /> */}
            {/* <Route path='/product/:brand/:slug/:id' component={ProductDetails} /> */}
            <AsyncNotFoundPage />
          </Switch>
        </Suspense>
        <AsyncFooter />
      </div>
    );
  }
}

function WaitingComponent(Component) {
  return (props) => (
    <Suspense fallback={<Loading />}>
      <Component {...props} />
    </Suspense>
  );
}

export default App;
