import React, { Component } from 'react';
import { Row, Col, Form, Button, Dropdown } from 'react-bootstrap';
import jwtDecode from 'jwt-decode'

// ... REDUX ...
import withReducer from "../../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../../store/reducers";
import * as Actions from "../../store/actions";
import asyncComponent from "./../../../AsyncComponent";

// ... COMPONENT ...
const AsyncContact = asyncComponent(() => import('./Contact'));
const AsyncChildProfile = asyncComponent(() => import('./ChildProfile'));
const AsyncAddress = asyncComponent(() => import('./Address'));
const AsyncPassword = asyncComponent(() => import('./Password'));
const AsyncSubscription = asyncComponent(() => import('./Subscription'));
const AsyncBio = asyncComponent(() => import('./Bio'));

// import Contact from 
// import  from 
// import  from 
// import  from 
// import  from 
// import  from 

class MyProfile extends Component {

    constructor(props)
    {
        super(props);
        this.state={
            userProfile: '',
            profileForm: false,
            childForm: false,
            addChildForm:false,
        }
    }

    componentDidMount() {
        this.props.getProfile();
    }

    static getDerivedStateFromProps(props, state) {
        return {
            userProfile: props.userProfile,
            Pswdmsg: props.Pswdmsg,
            Addressmsg: props.Addressmsg,
            Contactmsg: props.Contactmsg,
            Profilemsg: props.Profilemsg,
            Childmsg: props.Childmsg,
            variant: props.variant
        }
    }

    // ... HANDLERS ...

    handleSubmitSubscription = (data, subs) => {
        switch(subs)
        {
            case 'email':
                this.props.updateEmailMarketing(data);
            break;

            case 'sms':
                this.props.updateSmsMarketing(data);
            break;
        }
    }

    handleSubmitProfile = (data) => {

        this.props.updateProfile(data);

        this.setState({
            image:null,
            msg:'',
            profileForm: false
        })
    }

    handleSubmitContact = (dataArray) => {        

        this.props.updateContact(dataArray);
    }

    handleSubmitAddress = (data) => {
        this.props.updateAddress(data);
    }

    handleSubmitPswd = (crntPass, newPass, cnfrmPass) => {
        this.props.updatePassword(crntPass,newPass,cnfrmPass)
    }

    handleSubmitChild = (data, crud) => {
        switch(crud)
        {
            case 'ADD':
                this.props.addChild(data);
            break;

            case 'UPDATE':
                this.props.updateChild(data);
            break;

            case 'DELETE':
                // console.log(data);
                this.props.deleteChild(data);
            break;
        }
        console.log('CRUD :: ' + crud);
        this.setState({addChildForm : false })
    }

    toggleChild = () => {
        this.setState({
            addChildForm: !this.state.addChildForm
        })
    }

    toggleBio = () => {
        this.setState({
            profileForm: !this.state.profileForm
        })
    }

    render() {

        let user = this.state.userProfile ? this.state.userProfile : {} ;
        return (
            // <Provider>
            <div className="main-account profile">
                <h3>My Profile</h3>
                <div className="profile-container">       
                    <AsyncBio user={user} submitBio={this.handleSubmitProfile} Profilemsg={this.state.Profilemsg} variant={this.state.variant} toggleBio={this.toggleBio} profileForm={this.state.profileForm}/>
                    <AsyncContact user={user} submitContact={this.handleSubmitContact} ResendOTP={this.props.ResendOTP} Contactmsg={this.state.Contactmsg} variant={this.state.variant}/>
                    <AsyncChildProfile {...user} submitChild={this.handleSubmitChild} childMsg={this.state.Childmsg} variant={this.state.variant} toggleForm={this.toggleChild} addChildForm={(user.children && user.children.length > 0) ? this.state.addChildForm : !this.state.addChildForm}/>
                    <AsyncAddress user={user} submitAddress={this.handleSubmitAddress} Addressmsg={this.state.Addressmsg} variant={this.state.variant}/>
                    <AsyncPassword user={user} submitPassword={this.handleSubmitPswd} Pswdmsg={this.state.Pswdmsg} variant={this.state.variant}/>
                    <AsyncSubscription user={user} submitSubscription={this.handleSubmitSubscription} />
                </div>
            </div>
            // </Provider>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        userProfile: state.myProfile.account.userProfile,
        Pswdmsg: state.myProfile.account.Pswdmsg,
        Addressmsg: state.myProfile.account.Addressmsg,
        Contactmsg: state.myProfile.account.Contactmsg,
        Profilemsg: state.myProfile.account.Profilemsg,
        Childmsg: state.myProfile.account.Childmsg,
        variant: state.myProfile.account.variant
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        getProfile: Actions.getProfile,
        updateProfile: Actions.updateProfile,
        updateContact: Actions.updateContact,
        updateAddress: Actions.updateAddress,
        updatePassword: Actions.updatePassword,
        updateEmailMarketing: Actions.updateEmailMarketing,
        updateSmsMarketing: Actions.updateSmsMarketing,
        addChild: Actions.addChild,
        updateChild: Actions.updateChild,
        deleteChild: Actions.deleteChild,
        ResendOTP: Actions.ResendOTP
    }, dispatch)
}

export default withReducer('myProfile',reducer)
                (
                    connect(mapStateToProps, mapDispatchToProps)(MyProfile)
                )