import React, { Component } from "react";
import FacebookLogin from "react-facebook-login/dist/facebook-login-render-props";
import { FACEBOOK_APP_ID} from "./../../../config/app-config";

// ... REDUX ...
import withReducer from "../../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./../../store/reducers";
import * as Actions from "./../../store/actions";

class Facebook extends Component{
    constructor(props)
    {
        super(props);
    }

    responseFacebook = response => {
        this.props.SocialRegistration(response , '1' , '3'); // -- FOR SIGNUP
        // this.props.SocialLogin(response.id); // -- FOR LOGIN
    };

    render()
    {
        {/* FB APP ID 494340657971708 */}
        return (
            <FacebookLogin
                appId={FACEBOOK_APP_ID}
                fields="name,email"
                callback={this.responseFacebook}
                render={renderProps => (
                <a onClick={renderProps.onClick}>
                    {" "}
                    <img src="/assets/images/login-fb.jpg" alt="" />
                </a>
                )}
            />
        )
    }
}


const mapStateToProps = (state, ownProps) => {
    return {
        isAuthenticated: state.facebook.auth.isAuthenticated,
        user_object: state.facebook.auth.user_object,
        user: state.facebook.auth.user,
        msg: state.facebook.auth.msg
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        SocialRegistration: Actions.SocialRegistration,
    }, dispatch)
}

export default withReducer('facebook',reducer)
                (
                    connect(mapStateToProps,mapDispatchToProps)(Facebook)
                )