import React, {Component} from 'react'
import { GoogleLogin } from 'react-google-login';
import { GOOGLE_CLIENT_ID } from "./../../../config/app-config";

// ... REDUX ...
import withReducer from "../../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./../../store/reducers";
import * as Actions from "./../../store/actions";

class Gmail extends Component{
    constructor(props)
    {
        super(props);
    }

    responseGoogle = response => {
        this.props.SocialRegistration({name: response.profileObj.name , email: response.profileObj.email, id: response.profileObj.googleId} , '1' , '2'); // -- FOR SIGNUP
    }

    render()
    {
        return (
            <GoogleLogin
                clientId={GOOGLE_CLIENT_ID}
                render={renderProps => (
                <a onClick={renderProps.onClick}>
                    {" "}
                    <img src="/assets/images/login-gmail.jpg" alt="" />
                </a>
                )}
                buttonText="Login"
                onSuccess={this.responseGoogle}
                onFailure={this.responseGoogleFailiure}
                cookiePolicy={'single_host_origin'}
            />
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        isAuthenticated: state.gmail.auth.isAuthenticated,
        user_object: state.gmail.auth.user_object,
        user: state.gmail.auth.user,
        msg: state.gmail.auth.msg
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        SocialRegistration: Actions.SocialRegistration,
    }, dispatch)
}

export default withReducer('gmail',reducer)
            (
                connect(mapStateToProps,mapDispatchToProps)(Gmail)
            )
