import React, { Component } from "react";
import { LinkContainer } from "react-router-bootstrap";
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";
import { Button, OverlayTrigger, Popover } from "react-bootstrap";
import { bindActionCreators } from "redux";
import { hostUrl } from "./store/config";

import CartPopup from "./CartPopup";

class MiniCart extends Component {
  constructor(props) {
    super(props);
    this.state = {
      itemCount: 0,
      cartToggle: false,
    };
  }

  componentDidMount() {
    this.props.getCartFromCache();
  }

  static getDerivedStateFromProps(props, state) {
    // if (props.itemCount !== state.itemCount)
    // {

    return {
      itemCount: props.itemCount,
      cartItems: props.cartItems,
      procache: props.procache,
    };
    // }
  }

  handleShowCart = () => {
    this.setState({ cartToggle: true });
  };

  handleClose = () => {
    this.setState({ cartToggle: false });
  };

  render() {
    // let currentCache = JSON.parse(localStorage.getItem('procache'));
    let cartItems = null;
    if (this.props.cartItems) cartItems = this.props.cartItems;

    let { itemCount } = this.state;

    let totalAmount = 0;
    let discountAamount = 0;

    cartItems &&
      this.state.procache &&
      cartItems.map((item) => {
        return this.state.procache.map((pro) => {
          let discountAamount,
            totalAmount = 0;
          if (pro[0] === item.Id) {
            if (item["Sale Price"]) {
              // console.log('Haaan');
              discountAamount += item["Sale Price"] * pro[1];
              totalAmount += item["Price"] * pro[1];
            } else {
              // console.log('Naaaa');
              totalAmount += item["Price"] * pro[1];
            }
          }
          return [discountAamount, totalAmount];
        });
      });

    return (
      <>
        <OverlayTrigger
          trigger="hover"
          placement={"bottom"}
          delay={{ show: 250, hide: 2000 }}
          overlay={
            <Popover
              id={`mini-cart`}
              title={`Shopping Cart (${itemCount} Item)`}
            >
              <div className="inner-body">
                {cartItems &&
                  this.state.procache &&
                  // Object.keys(cartItems).length >= 1 &&
                  cartItems.map((item) =>
                    this.state.procache.map((pro) =>
                      pro[0] === item.Id ? (
                        <div
                          class="pro-content mx-4"
                          style={{
                            borderBottom: "1px solid #eeeeee",
                            paddingBottom: "10px",
                          }}
                        >
                          <div className="pro-img">
                            <a href="#a">
                              {/* {console.log('Images :: ' + item.Image)} */}
                              <img
                                src={`${hostUrl}${item.Image.split(",")[0]}`}
                                alt=""
                              />
                            </a>
                          </div>
                          <div className="pro-text">
                            <a href="#a">
                              <p className="pro-title font-weight-bold">
                                {item.Name}
                              </p>
                              <p className="pro-qty">Qty: {pro[1]}</p>
                              <p className="pro-qty">Rs: {item.Price}</p>
                            </a>
                          </div>
                        </div>
                      ) : (
                        ""
                      )
                    )
                  )}
              </div>
              <div className="btn-actions">
                <LinkContainer to="/checkout">
                  <Button className="btn-order">Place Order</Button>
                </LinkContainer>
                <Button className="btn-cart" onClick={this.handleShowCart}>
                  Edit Cart
                </Button>
              </div>
            </Popover>
          }
        >
          <div className="order">
            <div className="cart-icon">
              <div className="cart-count">{itemCount}</div>
            </div>
            <LinkContainer to="/checkout">
              <Button size="sm" className="btn-order">
                PLACE ORDER
              </Button>
            </LinkContainer>
          </div>
        </OverlayTrigger>

        <CartPopup
          handleShow={this.handleShow}
          handleClose={this.handleClose}
          show={this.state.cartToggle}
        />
      </>
    );
  }
}

function mapStateToProps(state) {
  // console.log(state);
  return {
    itemCount: Math.max(
      state.cartReducer.cart.itemCount ? state.cartReducer.cart.itemCount : 0,
      state.cartReducer.auth.itemCount ? state.cartReducer.auth.itemCount : 0
    ),
    cartItems: state.cartReducer.cart.cartItems,
    procache: state.cartReducer.cart.procache,
  };
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      getCartFromCache: Actions.getCartFromCache,
    },
    dispatch
  );
};

export default withReducer(
  "cartReducer",
  reducer
)(connect(mapStateToProps, mapDispatchToProps)(MiniCart));
