import React, { Component } from 'react'
import { Row, Col, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { hostUrl } from '../store/config';
import Moment from 'react-moment'

import AddressPopup from './AddressPopup';
import TrackPopup from './TrackPopup';

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";

class OrderHistory extends Component {
    constructor(props) {
        super(props);
        this.state = {
          data: [],
          procache: [],
          cookie: [],
          selectAll: false,
          selectedShortlistId: [],
          cartToggle: false,
          shortlist:false,
          trackToggle: false,
          addressToggle: false,
          status: -1,
          type: 'null'
        };
        this.handleShowTrack = this.handleShowTrack.bind(this);
      }

      componentDidMount() {
        // this.saveData();
        this.props.getOrderHistory();
      }

      static getDerivedStateFromProps(props, state) {
          return {
            order_history: props.order_history,
            userProfile: props.userProfile,
            Addressmsg: props.Addressmsg,
            variant: props.variant
          }
      }

      handleClickId = (value, checked) => {
        // console.log("check " + checked)
        let { selectedShortlistId } = this.state;
        if (checked) {
            selectedShortlistId.push(value);
        } else {
            selectedShortlistId.splice(selectedShortlistId.indexOf(value),1);
        }
        this.setState({ selectedShortlistId });
        // console.log("id selected " + this.state.selectedShortlistId)
    }

    handleSubmitAddress = (data) => {
        this.props.updateAddress(data);

        setTimeout(() => {
            this.setState({
                addressToggle : false
            })
        }, 3000)
    }
     
    handleShortlistStatus = () => {
        this.setState({
            shortlist: !this.state.shortlist
        })
    }

    handleShowCart = (product, pro_qty) => {
        this.props.saveDataToCart(product,pro_qty);
        this.setState({ cartToggle: true });
    }

    handleClose = () => {
        this.setState({ cartToggle: false });
    }

    handleOrderFilter = (evt) => {
        this.props.getOrderHistory(evt.target.value, this.state.type)
        this.setState({
            status: evt.target.value
        })
    }

    handleOrderDate = (evt) => {
        this.props.getOrderHistory(this.state.status, evt.target.value)
        this.setState({
            type: evt.target.value
        })
    }

    handleShowTrack = (product, pro_qty) => {
        // this.props.saveDataToTrack(product,pro_qty);
        this.setState({ trackToggle: true });
    }

    handleCloseTrack = () => {
        this.setState({ trackToggle: false });
    }

    handleShowAddress = () => {
        this.setState({ addressToggle: true });
    }

    handleCloseAddress = () => {
        this.setState({ addressToggle: false });
    }  

    getOrderStatus(status)
    {
        let status_dictionary = [
            {code: 'Pending', color:'grey'}, 
            {code: 'Delivered', color: 'green'}, 
            {code: 'In Process', color: 'blue'}, 
            {code: 'Cancelled', color: 'red'}, 
            {code: 'Dispatched', color: 'orange'}
        ]

       return status_dictionary[status]
    }


    render() {
        let history = this.state.order_history ? this.state.order_history : []
        return (
            <>
                <div className="main-account profile">
                    <h3>Order History</h3>
                    <div className="order-filter">
                        <Form 
                        ref={form => (this.form = form)}>
                            <Row>
                                <Col md={3}>
                                    <Form.Group as={Row} controlId="fitler">
                                        <Form.Label column sm={12}>
                                            Filter Order By
                                        </Form.Label>
                                    </Form.Group>
                                </Col>
                                <Col md={4}>
                                    <Form.Group as={Row} controlId="type">
                                        <Form.Label column sm={3}>
                                            Type:
                                        </Form.Label>
                                        <Col sm={9}>
                                        <Form.Control as="select" name='type' onChange={this.handleOrderFilter}>
                                            <option value={-1}>All Orders</option>
                                            <option value={0}>Pending</option>
                                            <option value={2}>In Process</option>
                                            <option value={4}>Dispacthed</option>
                                            <option value={3}>Cancelled</option>
                                            <option value={1}>Delivered</option>
                                        </Form.Control>
                                        </Col>
                                    </Form.Group>
                                </Col>
                                <Col md={5}>
                                    <Form.Group as={Row} controlId="timeperiod">
                                        <Form.Label column sm={4}>
                                            Time Period:
                                        </Form.Label>
                                        <Col sm={8}>
                                        <Form.Control as="select" name='timeperiod' onChange={this.handleOrderDate}>
                                            <option value='today'>Today</option>
                                            <option value='week'>Last Week</option>
                                            <option value='month'>Last 30 Days</option>
                                            <option value='all'>View All</option>
                                        </Form.Control>
                                        </Col>
                                    </Form.Group>
                                </Col>
                            </Row>
                        </Form>
                    </div>

                    {history && history.map(order => {
                            return <Row className="order-product">
                            <Col md={12} className="border-bottom order-info">
                                <Row>
                                    <Col md={6} className="d-flex flex-column align-items-center align-items-md-start">
                                        <span>Order Number : <span><b>{order.order_no}</b></span></span>
                                        <span>Order Placed : <span className="black"><Moment date={order.order_date}/></span></span>
                                    </Col>
                                    <Col md={6} className="d-flex flex-column align-items-center align-items-md-end">
                                        <span>Order Status : <span style={{color:this.getOrderStatus(order.order_status).color}}><b>{this.getOrderStatus(order.order_status).code}</b></span></span>
                                        <span>Order Total : <span className="black">Rs {order.total_bill}</span></span>
                                    </Col>
                                </Row>
                            </Col>
                            <Col md={12} className="border-bottom order-actions">
                                <Row>
                                    <Col md={3} className="d-flex flex-column align-items-center align-items-md-start">
                                        <span className="black">{order.total_items} items in this Order</span>
                                    </Col>
                                    <Col md={9} className="d-flex flex-column align-items-center align-items-md-end">
                                        <div className="d-flex action-list">
                                            <span><Link to={{
                                                pathname: `/myaccount/orderdetails/${order.order_id}`,
                                                state: {id: order.order_id}
                                            }}>View Order Details </Link></span> 
                                            <span><a as={Link} onClick={ () => {
                                                        this.props.getProfile();
                                                        this.setState({
                                                            addressToggle: true,
                                                            // address_order: address,
                                                            // address_detail: order.order_details
                                                        })
                                                    }} > Change Address </a></span> 
                                            <span><a as={Link} onClick={ () => {
                                                        this.setState({
                                                            trackToggle: true,
                                                            track_order: order,
                                                            track_detail: order.order_details
                                                        })
                                                    }} > Track </a></span> 
                                            <span><a href="#a"> Cancel </a></span> 
                                            {/* <span><a href="#a"> Return </a></span>  */}
                                            {/* <span><Link to="/myaccount/reorder"> Reorder </Link></span> */}
                                        </div>
                                    </Col>
                                </Row>
                            </Col>
                            <Col md={12} className="order-gallery">
                                <Row className="m-0">
                                    {order.order_details.map(item => {
                                        return <Col md={2} className="order-img">
                                            <img alt='Item' src={hostUrl+(item.item_image).split(',')[0]} />
                                        </Col>
                                    })}
                                    {/* <Col md={2} className="order-img">
                                        <img src="https://lagari.pk/products/1563958351643-MMF0001_1.jpg" />
                                    </Col> */}
                                </Row>
                            </Col>
                        </Row>
                    })}
                </div>
                <TrackPopup handleShow={this.handleShowTrack} handleClose={this.handleCloseTrack} show={this.state.trackToggle || false} order={this.state.track_order} detail={this.state.track_detail}/>
                <AddressPopup user={this.state.userProfile ? this.state.userProfile : {}} Addressmsg={this.state.Addressmsg} variant={this.state.variant} 
                submitAddress={this.handleSubmitAddress} handleShow={this.handleShowAddress} handleClose={this.handleCloseAddress} 
                show={this.state.addressToggle || false} order={this.state.track_order} detail={this.state.address_detail}/>
            </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        order_history: state.order_history.order.order_history,
        userProfile: state.order_history.account.userProfile,
        Addressmsg: state.order_history.account.Addressmsg,
        variant: state.order_history.account.variant
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        getOrderHistory: Actions.getOrderHistory,
        getProfile: Actions.getProfile,
        updateAddress: Actions.updateAddress
    }, dispatch)
}

export default withReducer('order_history', reducer)(connect(mapStateToProps, mapDispatchToProps)(OrderHistory))