import React, { Component } from 'react'
import {Form, Button} from 'react-bootstrap'

import SignupNotification from '../SignupNotificatino'

// ... REDUX ...
import withReducer from "../../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./../../store/reducers";
import * as Actions from "./../../store/actions";

class RegisterForm extends Component {

    constructor(props)
    {
        super(props);

        this.state={
            agree: false,
            msg:null
        }
    }

    static getDerivedStateFromProps(props, state) {
        return {
            signup_msg: props.signup_msg,
            variant: props.variant
        }
    }

    handleSignUpClick = (event) => {
        let {email, mob_prefix, mobile, password, confirmPswd} = this.signupForm;
    
          if(this.state.agree)
          {
            if(password.value === confirmPswd.value)
            {
              let formBody = new FormData();

              let name = (email.value).split('@')[0];
              // console.log(name);
              formBody.append('name',name);
              formBody.append('email', email.value);
              formBody.append('phone', mob_prefix.value + mobile.value);
              formBody.append('password', confirmPswd.value);
              formBody.append('source', '1');
              formBody.append('signup_with', '1');
      
              this.props.SignUp(formBody);
  
            //   setTimeout(function(){
            //       this.props.stateNull();
            //  }.bind(this),3000); 
            }
            else
            {
              this.props.showSignupResponseMessage('Confirm password does not match','danger')
            }
          }
          else
          {
            this.props.showSignupResponseMessage('Please agree the terms and conditions below the signup form ....', 'danger')
            // alert('Please agree the terms and conditions below the signup form ....');
          }
    }

    render() {
        return (<>
            <SignupNotification />
            <Form className="register ml-lg-0 pl-lg-5"
                  ref={signupForm => (this.signupForm = signupForm)}>
                  <Form.Control
                    type="email"
                    autocomplete="off"
                    placeholder="Email id*"
                    maxlength="50"
                    size="25"
                    name="email"
                    autocorrect="off"
                    autocapitalize="off"
                    required
                  />
                  <div className="d-flex form">
                    <Form.Control
                      className="mobile-prefix"
                      type="tel"
                      name='mob_prefix'
                      placeholder="+92"
                      maxlength="2"
                      value="+92"
                    />
                    <Form.Control
                      className="mobile-no"
                      type="number"
                      placeholder="Mobile No.*"
                      maxlength="10"
                      name="mobile"
                      size="25"
                    />
                  </div>
                  <Form.Control
                    className="password"
                    type="password"
                    name='password'
                    placeholder="Password*"
                  />
                  <Form.Control
                    className="vpassword"
                    type="password"
                    name='confirmPswd'
                    placeholder="Verify Password*"
                  />
                  <Form.Control as='select'>
                    <option>Where did you here about us*</option>
                    <option>Internet</option>
                    <option>Television</option>
                    <option>Newspaper/Magazines</option>
                    <option>Friends or Family</option>
                    <option>Online Ads</option>
                    <option>Others</option>
                  </Form.Control>
                  <div className="register-check mb-2">
                    <div
                      onClick={() => {
                        this.setState({ agree: !this.state.agree });
                      }}
                      className={
                        this.state.agree === true
                          ? "bg-check check"
                          : "bg-check"
                      }
                    />
                    <span>
                      I read / agree to the{" "}
                      <a href="#a">
                        <b>Terms of Use</b>
                      </a>
                    </span>
                  </div>
                  <div className="register-check mb-4">
                    <div
                      onClick={() => {
                        this.setState({ offers: !this.state.offers });
                      }}
                      className={
                        this.state.offers === true
                          ? "bg-check check"
                          : "bg-check"
                      }
                    />
                    <span>Sign in for special offers from Babynestboutique.com</span>
                  </div>
                  {/* <p style={{color:`${this.state.variant}`}} >{this.state.signup_msg}</p> */}
                  <Button className="btn-submit" onClick={this.handleSignUpClick}>SIGN UP</Button>
                </Form>
              </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        user: state.registerForm.auth.user,
        signup_msg: state.registerForm.auth.signup_msg,
        variant: state.registerForm.auth.variant
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        SignUp: Actions.SignUp,
        stateNull: Actions.stateNull,
        showSignupResponseMessage: Actions.showSignupResponseMessage
    }, dispatch)
}

export default withReducer('registerForm',reducer)
            (
                connect(mapStateToProps,mapDispatchToProps)(RegisterForm)
            )
// export default RegisterForm
