import React, { Component } from 'react'
import { Row, Col, Button, Table } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import Moment from 'react-moment'
import { hostUrl } from '../store/config';
import _ from 'lodash'

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";

class CashCoupons extends Component {
    constructor(props) {
        super(props);
        this.state = {

        };
      }

      componentDidMount() {
          this.props.getCoupons()
      }

      static getDerivedStateFromProps(props, state) {
          return {
            cash_coupons : props.cash_coupons
          }
      }

      getTotalCouponsCash = (coupons) => {
        return _.sumBy(coupons.filter(coupon => coupon.cop_type === 'Direct'), (coupon) => coupon.cop_discount)
      }
    
    render() {
        let coupons = this.state.cash_coupons ? this.state.cash_coupons : null
        return (
            <>
                <div className="main-account profile">
                    <h3>Cash Coupons</h3>
                    {coupons ?
                        <div className="order-filter">
                            You have Rs {this.getTotalCouponsCash(coupons)} worth of Direct Cash Coupons.
                        </div>
                        :
                        <div className="order-filter bg-red">
                            Sorry No Coupons found...
                        </div>
                    }
                    <Row>
                        {coupons && coupons.map(coupon => 
                            <Col className="pt-4" md={4}>
                                <Table striped bordered hover>
                                    <tbody>
                                        <tr>
                                        <td>Coupon Code</td>
                                        <td>{coupon.cop_code}</td>
                                        </tr>
                                        <tr>
                                        <td>Description</td>
                                        <td>{coupon.cop_type === 'Direct' ? `Rs ${coupon.cop_discount} OFF*` : `${coupon.cop_discount} % OFF`}  after applying this code on specific products. </td>
                                        </tr>
                                        {/* <tr>
                                        <td>Valid Till</td>
                                        <td>06-Oct-2020 (2 Days left)</td>
                                        </tr> */}
                                        <tr>
                                        <td>{coupon.cop_type === 'Direct' ? `Amount(Rs)` : `Percent(%)`}</td>
                                        <td>{coupon.cop_type === 'Direct' ? `Rs. ${coupon.cop_discount}` : `${coupon.cop_discount} %`}</td>
                                        </tr>
                                        {/* <tr>
                                        <td></td>
                                        <td><Button className="btn-order">Apply Code</Button></td>
                                        </tr> */}
                                    </tbody>
                                </Table>
                            </Col>
                        )}
                        
                        {/* <Col className="pt-4" md={4}>
                            <Table striped bordered hover>
                                <tbody>
                                    <tr>
                                    <td>Coupon Code</td>
                                    <td>FACh535</td>
                                    </tr>
                                    <tr>
                                    <td>Description</td>
                                    <td>Rs 350 OFF* on the order on minimum ourchase worth Rs 999 </td>
                                    </tr>
                                    <tr>
                                    <td>Valid Till</td>
                                    <td>06-Oct-2020 (2 Days left)</td>
                                    </tr>
                                    <tr>
                                    <td>Amount(Rs)</td>
                                    <td>350</td>
                                    </tr>
                                    <tr>
                                    <td></td>
                                    <td><Button className="btn-order">Apply Code</Button></td>
                                    </tr>
                                </tbody>
                            </Table>
                        </Col> */}
                    </Row>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        cash_coupons: state.cashCoupons.account.cash_coupons,
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        getCoupons: Actions.getCoupons
    }, dispatch)
}

export default withReducer('cashCoupons', reducer)(connect(mapStateToProps, mapDispatchToProps)(CashCoupons))