import React, { Component } from "react";
import { Modal, Button, Table, Tabs, Tab } from "react-bootstrap";
import Moment from 'react-moment'

class TrackPopup extends React.PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      shortlist: false,
      value: 1,
      data: [],
      zip: false,
      procache: [],
      total: 0,
      orderDetailShow: false,
      summary: { 
        totalValue: 0, 
        discount: 0, 
        shipping_charges: 0, 
        estimated_gst: 0, 
        final_payment: 0
      }
    };
  }

  // In progress...

  // Dispatched ...

  // Delivered ...

  handleTrack = () => {
    let order = this.props.order

    let track = {
      inprocess : -1, 
      dispatched : -1, 
      delivered : -1
    }

    switch(order.order_status)
    {
      case 0: // pending...
          // all track grey...
          track.inprocess = 0 
          track.dispatched = 0 
          track.delivered = 0
          return track
        break;

      case 1: // delivered...
          // all track green...
          track.inprocess = 1 
          track.dispatched = 1 
          track.delivered = 1
          return track
        break;

      case 2: // In process...
          // only in process green
          track.inprocess = 1 
          track.dispatched = 0 
          track.delivered = 0
          return track
        break;

      case 4: // dispatched...
          // only delivered will grey
          track.inprocess = 1 
          track.dispatched = 1 
          track.delivered = 0
          return track
        break;

      default:
        return track

    }
  }

  getOrderStatus(status)
    {
        let status_dictionary = [
            {code: 'Pending', color:'grey'}, 
            {code: 'Delivered', color: 'green'}, 
            {code: 'In Process', color: 'blue'}, 
            {code: 'Cancelled', color: 'red'}, 
            {code: 'Dispatched', color: 'orange'}
        ]

       return status_dictionary[status]
    }

  render() {
    // let track = {
    //   inprocess: -1,
    //   delivered: -1,
    //   dispatched: -1
    // }
      let track = this.props.order && this.handleTrack()
      console.log('Order :: ' + JSON.stringify(this.props.order))
    return (
      <>
      {this.props.order && 
        <Modal
          className="cust-modal track-modal"
          show={this.props.show}
          onHide={() => {
            this.setState({
              orderDetailShow: false
            })
            this.props.handleClose()}}
          size="lg"
        >
          <div className="btn-close">
            <Button
              type="button" 
              className="close"
              onClick={() => {
                this.setState({
                  orderDetailShow: false
                })
                this.props.handleClose()}}
            >
              <span aria-hidden="true">×</span>
              <span className="sr-only">Close</span>
            </Button>
          </div>

          <Modal.Body>
              <div className="px-4 pt-4 pb-5 d-flex justify-content-between track-process">
                <div>
                  <span className={`circle ${track.inprocess === 1 ? 'green' : 'gray'}`}></span>
                  <span>In Procress</span>
                </div>
                <div>
                  <span className={`circle ${track.dispatched === 1 ? 'green' : 'gray'}`}></span>
                  <span>Dispatched</span>
                </div>
                {/* <div>
                  <span className="active"></span>
                  <span>In Transit</span>
                </div> */}
                {/* <div>
                  <span className="circle gray"></span>
                  <span>Out for delivery</span>
                </div> */}
                <div>
                  <span className={`circle ${track.delivered === 1 ? 'green' : 'gray'}`}></span>
                  <span>Delivered</span>
                </div>
              </div>
              <div className="px-4 pt-4 pb-3 border-top">
                <p className={ this.state.orderDetailShow === true ? "text-center track-more active": "text-center track-more" }><span 
                  onClick={()=>{
                      this.setState({
                        orderDetailShow: true
                      })
                    }
                  }>
                  Click Here</span> to see complete tracking history</p>
                  
                  {
                    this.state.orderDetailShow === true ? 
                      <div className="track-details">
                        <Table hover>
                          <thead>
                            <tr>
                              <th><b>Order Date: </b></th>
                              <th><b>Products: </b></th>
                              <th><b>Total: </b></th>
                              <th><b>Status: </b></th>
                            </tr>
                          </thead>
                            <tbody>
                                <tr>
                                  <td><p><Moment date={this.props.order.order_date} filter={(d) => d.toUpperCase()}/></p></td>
                                  <td>{this.props.detail.map(item => 
                                    {return <p>{item.item_name}</p>}
                                    )}
                                  </td>
                                  <td><p>{this.props.order.total_bill}</p></td>
                                  <td><p style={{color:this.getOrderStatus(this.props.order.order_status).color}}><b>{this.getOrderStatus(this.props.order.order_status).code}</b></p></td>
                                </tr>
                            </tbody>
                        </Table>
                      </div>
                    : ""
                  }
              </div>
              
          </Modal.Body>
        </Modal>
    }
      </>
    );
  }
}


export default TrackPopup;