import React, { Component } from 'react';
import {Alert, Button} from "react-bootstrap";

// ... REDUX ...
import withReducer from "./../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./../store/reducers";
import * as Actions from "./../store/actions";

class SignupNotification extends Component {
    constructor(props) {
        super(props);
        this.state = {
          response_show: false,
          response_message: '',
          variant: '',
        };
      }

      static getDerivedStateFromProps(props, state) {
        let show = null;
        if(props.response_message && (props.response_message.length >0) )
        {
          show = true;
          window.scrollTo(0,0)
        }
        else
        {
          show = false;
        }
        return {
          response_message: props.response_message,
          response_show: show,
          variant: props.variant
        }
      }

      variants = {
        success: 'success',
        error: 'danger',
        info:'blue',
        warning:'yellow'
      }

      handleDismiss = () => {
        this.props.hideSignupResponseMessage();
      }

    render() {

        if(this.state.response_show === true)
        {
          setTimeout(() => {this.props.hideSignupResponseMessage()}, 5000);
        }        
       
        if (this.state.response_show) {
          return (
            <Alert variant={this.state.variant && this.state.variant} onClose={this.handleDismiss} className="top-notification text-center" dismissible>
              <p>
                {this.state.response_message && this.state.response_message}
              </p>
            </Alert>
          );
        }
        return <></>
        // return <Button onClick={handleShow}>Show Alert</Button>;
    }
}

const mapStateToProps = (state, ownProps) => {
  return {
    response_message: state.SignupNotificationTab.notification.signup_response_message,
    variant: state.SignupNotificationTab.notification.signup_variant
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
    hideSignupResponseMessage: Actions.hideSignupResponseMessage
  }, dispatch)
}

export default withReducer('SignupNotificationTab', reducer)
            (
              connect(mapStateToProps, mapDispatchToProps)(SignupNotification)
            )

