import React, { Component } from "react";
import { Modal, Button, Form, Row, Col } from "react-bootstrap";
import Moment from 'react-moment'
import jwtDecode from 'jwt-decode'
import cities from '../../cities';

class AddressPopup extends Component {
 
  constructor(props) {
    super(props);

    this.state = {
      user: {}
    };
  }

  static getDerivedStateFromProps(props, state) {
    // console.log('PROPS :: ' + JSON.stringify(props.user));
    return {
      user : props.user,
      Addressmsg: props.Addressmsg,
      variant: props.variant,
    }
  }

  handleTrack = () => {
    let order = this.props.order

    let track = {
      inprocess : -1, 
      dispatched : -1, 
      delivered : -1
    }

    switch(order.order_status)
    {
      case 0: // pending...
          // all track grey...
          track.inprocess = 0 
          track.dispatched = 0 
          track.delivered = 0
          return track
        break;

      case 1: // delivered...
          // all track green...
          track.inprocess = 1 
          track.dispatched = 1 
          track.delivered = 1
          return track
        break;

      case 2: // In process...
          // only in process green
          track.inprocess = 1 
          track.dispatched = 0 
          track.delivered = 0
          return track
        break;

      case 4: // dispatched...
          // only delivered will grey
          track.inprocess = 1 
          track.dispatched = 1 
          track.delivered = 0
          return track
        break;

      default:
        return track

    }
  }

  handleAddressChange = (event) => {
    let updatedUser = this.state.user;

    switch(event.target.name)
    {
        case 'house':
            updatedUser.cust_address.house_no = event.target.value;
            console.log('House no :: ' + event.target.value);
        break;

        case 'street':
            updatedUser.cust_address.streetAddress = event.target.value;
            console.log('street :: ' + event.target.value);
        break;

        case 'pincode':
            updatedUser.cust_zip = event.target.value;
            console.log('Pincode :: ' + event.target.value);
        break;

        case 'city':
            updatedUser.cust_city = event.target.value;
            console.log('City :: ' + event.target.value);
        break;

        case 'state':
            updatedUser.cust_address.state = event.target.value;
            console.log('State :: ' + event.target.value);
        break;

        default:
          return {}

    }

    this.setState({
        user: updatedUser
    }, () => {
      console.log('USER STATE :: ' + JSON.stringify(this.state.user))
      console.log('PROPS STATE :: ' + JSON.stringify(this.props.user))
    })
}

handleSubmit = (event) => {
  let formBody = new FormData();
  let {country} = this.form; 
  if(localStorage.getItem('jwtToken'))
  {
      let cust_id = (jwtDecode(localStorage.getItem('jwtToken'))).cust_id;
      formBody.append('cust_id',cust_id)
  }

  formBody.append('house_no', this.state.user.cust_address.house_no);
  formBody.append('address', this.state.user.cust_address.streetAddress);
  formBody.append('pincode', this.state.user.cust_zip);
  formBody.append('city', this.state.user.cust_city);
  formBody.append('state', this.state.user.cust_address.state);
  formBody.append('country', country.value);

  this.props.submitAddress(formBody);
}

  getOrderStatus(status)
    {
        let status_dictionary = [
            {code: 'Pending', color:'grey'}, 
            {code: 'Delivered', color: 'green'}, 
            {code: 'In Process', color: 'blue'}, 
            {code: 'Cancelled', color: 'red'}, 
            {code: 'Dispatched', color: 'orange'}
        ]

       return status_dictionary[status]
    }

  render() {
    // let track = {
    //   inprocess: -1,
    //   delivered: -1,
    //   dispatched: -1
    // }
      let track = this.props.order && this.handleTrack()
      console.log('Order :: ' + JSON.stringify(this.props.order))
    return (
      <>
        <Modal
          className="cust-modal address-modal"
          show={this.props.show}
          onHide={() => {
            this.setState({
              orderDetailShow: false
            })
            this.props.handleClose()}}
          size="lg"
        >
          <div className="btn-close">
            <Button
              type="button" 
              className="close"
              onClick={() => {
                this.setState({
                  orderDetailShow: false
                })
                this.props.handleClose()}}
            >
              <span aria-hidden="true">×</span>
              <span className="sr-only">Close</span>
            </Button>
          </div>

          <Modal.Body>
              
          <div id="address" className="profile">
                <div className="head">Update Address</div> 
                <Row className="m-1 pt-3">
                    <Col md={7}>
                        <Form 
                            ref={form => (this.form = form)}>
                            <Form.Group as={Row} controlId="address-name" className="mb-4">
                                <Form.Label column sm={3}>
                                    Name:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="text" name="address-name" placeholder={(this.state.user && this.state.user.cust_name) ? this.state.user.cust_name : ''} disabled />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="house" className="mb-2">
                                <Form.Label column sm={3}>
                                    Flat/House No./ Building:
                                </Form.Label>
                                <Col sm={9}>
                                <Form.Control type="text" name="house" 
                                        placeholder={(this.state.user && this.state.user.cust_address && this.state.user.cust_address.house_no) ? this.state.user.cust_address.house_no : ''}
                                        value={(this.state.user && this.state.user.cust_address && this.state.user.cust_address.house_no) ? this.state.user.cust_address.house_no : ''}
                                        onChange={this.handleAddressChange} />
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="street" className="mb-2">
                                <Form.Label column sm={3}>
                                    Street Address / Colony:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="text" name="street" 
                                        placeholder={(this.state.user && this.state.user.cust_address && this.state.user.cust_address.streetAddress) ? this.state.user.cust_address.streetAddress : ''}
                                        value={(this.state.user && this.state.user.cust_address && this.state.user.cust_address.streetAddress) ? this.state.user.cust_address.streetAddress : ''}
                                        onChange={this.handleAddressChange}/>
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="pincode" className="mb-4">
                                <Form.Label column sm={3}>
                                    Pincode:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="text" name="pincode" 
                                        placeholder={(this.props.user && this.props.user.cust_zip) ? this.props.user.cust_zip : ''}
                                        value={(this.props.user && this.props.user.cust_zip) ? this.props.user.cust_zip : ''}
                                        onChange={this.handleAddressChange}/>
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="city" className="mb-4">
                                <Form.Label column sm={3}>
                                    City:
                                </Form.Label>
                                <Col sm={9}>
                                <Form.Control as="select" name='city' onChange={this.handleAddressChange}>
                                    {cities && cities.cities.map((city,key) =>
                                        {if((this.props.user && this.props.user.cust_city) && this.props.user.cust_city === city)
                                        {
                                            return(<option key={key} selected>{this.props.user.cust_city}</option>)
                                        }
                                        else
                                        {
                                            return(<option key={key}>{city}</option>)    
                                        }
                                        }
                                    )}
                                </Form.Control>
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="state" className="mb-4">
                                <Form.Label column sm={3}>
                                    State:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="text" name="state" 
                                        placeholder={(this.props.user && this.props.user.cust_address && this.props.user.cust_address.state) ? this.props.user.cust_address.state : ''}
                                        value={(this.props.user && this.props.user.cust_address && this.props.user.cust_address.state) ? this.props.user.cust_address.state : ''}
                                        onChange={this.handleAddressChange}/>
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="country" className="mb-4">
                                <Form.Label column sm={3}>
                                    Country:
                                </Form.Label>
                                <Col sm={9}>
                                    <Form.Control type="text" name="country" disabled
                                        placeholder={(this.props.user && this.props.user.cust_address && this.props.user.cust_address.country) ? this.props.user.cust_address.country : 'Pakistan'}
                                        value={(this.props.user && this.props.user.cust_address && this.props.user.cust_address.country) ? this.props.user.cust_address.country : 'Pakistan'}
                                        onChange={this.handleAddressChange}/>
                                </Col>
                            </Form.Group>
                            <Form.Group as={Row} controlId="mobile">
                                <Form.Label column sm={3} className="pr-0">
                                    Mobile No:
                                </Form.Label>
                                <Col sm={9} >
                                    <div className="d-flex mb-4 align-items-center">
                                        <Form.Control className="mobile-prefix" type="tel" maxlength="2"
                                            placeholder={(this.props.user && this.props.user.cust_phone) ? this.props.user.cust_phone.substring(0,3): ''} disabled/>
                                        <Form.Control className="mobile-no" type="tel" maxlength="10" 
                                            placeholder={(this.props.user && this.props.user.cust_phone) ? this.props.user.cust_phone.substring(3): ''} disabled/>
                                    </div>
                                    <p style={{color:`${this.props.variant}`}} >{this.props.Addressmsg}</p>
                                    <Button className="btn-submit" onClick={this.handleSubmit}>Update</Button>
                                    {/* <Button className="btn-submit btn-cancel" type="submit">Cancel</Button> */}
                                </Col>
                            </Form.Group>
                        </Form>
                    </Col>
                    <Col md={5}></Col>
                </Row>
            </div>
              
          </Modal.Body>
        </Modal>
      </>
    );
  }
}


export default AddressPopup;