import React, { Component } from 'react'
import { Row, Col, Button, Form } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import ShortlistProduct from './ShortlistProduct';
import Product from "../../product.json";
import Cookies from 'universal-cookie';
import Moment from 'react-moment'

import CartPopup from '../CartPopup';
import TrackPopup from './TrackPopup';

import {
    getLocalStorageObject,
    getLocalStorageString
} from '../Utilities'

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "../store/reducers";
import * as Actions from "../store/actions";
import { hostUrl } from '../store/config';

class OrderDetails extends Component {
    constructor(props) {
        super(props);
        this.state = {
          data: [],
          procache: [],
          cookie: [],
          selectAll: false,
          selectedShortlistId: [],
          cartToggle: false,
          shortlist:false
        };
      }

      componentDidMount() {

        
        // const { id } = this.props.location.state
        const { order_id } = this.props.match.params

        if(order_id)
        {
            this.props.getOrderDetail(order_id);
        }
        else
        {
            this.props.showResponseMessage('Sorry you cannot access this page...', 'danger')
        }
      }

      static getDerivedStateFromProps(props, state) {
          return {
              order_detail : props.order_detail
          }
      }

      getOrderStatus(status)
        {
            let status_dictionary = [
                {code: 'Pending', color:'grey'}, 
                {code: 'Delivered', color: 'green'}, 
                {code: 'In Process', color: 'blue'}, 
                {code: 'Cancelled', color: 'red'}, 
                {code: 'Dispatched', color: 'orange'}
            ]

        return status_dictionary[status]
        }

      handleClickId = (value, checked) => {
        // console.log("check " + checked)
        let { selectedShortlistId } = this.state;
        if (checked) {
            selectedShortlistId.push(value);
        } else {
            selectedShortlistId.splice(selectedShortlistId.indexOf(value),1);
        }
        this.setState({ selectedShortlistId });
        // console.log("id selected " + this.state.selectedShortlistId)
    }

    handleShortlistStatus = () => {
        this.setState({
            shortlist: !this.state.shortlist
        })
    }

    handleShowCart = (product, pro_qty) => {
        this.props.saveDataToCart(product,pro_qty);
        this.setState({ cartToggle: true });
    }

    handleClose = () => {
        this.setState({ cartToggle: false });
    }

    handleShowTrack = (product, pro_qty) => {
        // this.props.saveDataToTrack(product,pro_qty);
        this.setState({ trackToggle: true });
    }

    handleCloseTrack = () => {
        this.setState({ trackToggle: false });
    }   

    handleStatus = (order_id, status) => {
        this.props.updateOrderStatus(order_id, status)
    }
    
    saveData = () => {
    const cookies = new Cookies();
    return this.setState({
        procache: getLocalStorageObject('procache'),
        data: getLocalStorageObject('shortlist'),
        cookie: cookies.get('shortlist')
    });
    };

    render() {
        const cookies = new Cookies();
        const currentCookies = getLocalStorageObject('shortlist');
        let currentCache = getLocalStorageObject('procache');

        let order = this.state.order_detail && this.state.order_detail.OrderStats
        let order_detail = this.state.order_detail && this.state.order_detail.OrderDetails
        let misc = this.state.order_detail && this.state.order_detail.Misc
        return (
            <>
                <div className="main-account profile">
                    <h3>Order Details</h3>
                    <Row className="order-product details">
                        <Col md={6} className="d-flex flex-column align-items-center align-items-md-start pb-3">
                            <table>
                                <tbody>
                                    <tr>
                                        <td><span>Order Number </span></td><td><span> : <b> {order && order.order_no}</b></span></td>
                                    </tr>
                                    <tr>
                                        <td><span>Order Placed </span></td><td> : <span className="black"> {order && <Moment date={order.order_date}/>}</span> | <span> {order && order.total_items} item</span></td>
                                    </tr>
                                    <tr>
                                        <td><span>Order Status </span></td><td><span style={{color: order ? this.getOrderStatus(order.order_status).color : 'black'}}> : <b> {order && this.getOrderStatus(order.order_status).code}</b></span></td>
                                    </tr>
                                    <tr>
                                        <td><span>Order Total </span></td><td> : <span className="black"> {order && `Rs ${order.total_bill}`}</span></td>
                                    </tr>
                                </tbody>
                            </table>
                        </Col>
                        <Col md={6} className="d-flex flex-column align-items-center align-items-md-end">
                            {order && (order.order_status === 0 || order.order_status === 2) && <button type="button" class="btn btn-outline-primary" onClick={() => {this.handleStatus(order && order.order_id, 3)}}>CANCEL ORDER</button>}
                        </Col>
                    </Row>

                    <Row className="order-product">
                        <Col md={12} className="order-gallery info">
                            <Row className="m-0">
                                <Col md={2} className="order-img">
                                    {order_detail && order_detail.map(item => {
                                        return <img alt='product' src={hostUrl+item.item_image.split(',')[0]} />
                                    })}
                                </Col>
                                <Col md={8} className="order-details">
                                    <div>
                                        {order_detail && order_detail.map(item => {
                                            return <><p>{item.item_name}</p>
                                            <p><b>Rs {item.item_total_price}</b></p>
                                            <div className="order-att">
                                                {/* <span>Size: <b>NB</b></span>
                                                <span>Color: <span className="pro-color" style={{"background": "red"}}></span></span> */}
                                                <span>Qty: <b>{item.item_qty}</b></span>
                                            </div></>}
                                        )}
                                        {/* <div className="order-status">
                                            <span>Cancelled Quantity: <b>1</b></span>
                                        </div> */}
                                        <div className="order-action-btn">
                                            {/* {order && (order.order_status === 1) && <Link to="/myaccount/reorder" class="btn reorder btn-outline-primary">REORDER</Link>} */}
                                            <a as={Link} onClick={ () => {
                                                        this.setState({
                                                            trackToggle: true,
                                                            track_order: order,
                                                            track_detail: order_detail 
                                                        })
                                                    }} class="btn track bg-orange">TRACK</a>
                                            {order && (order.order_status === 0 || order.order_status === 2) && <Link to="" class="btn cancel btn-outline-primary" onClick={() => {this.handleStatus(order && order.order_id, 3)}}>CANCEL</Link>}
                                        </div>
                                        <div className="pb-3">
                                            <table>
                                                <tbody>
                                                    {/* <tr>
                                                        <td><span>AWB No </span></td><td><span> : <b> 9343425345</b></span></td>
                                                    </tr> */}
                                                    <tr>
                                                        <td><span>Shipment Status </span></td><td> : <span style={{color: order ? this.getOrderStatus(order.order_status).color : 'black'}}><b> {order && this.getOrderStatus(order.order_status).code} </b></span></td>
                                                    </tr>
                                                    {/* <tr>
                                                        <td><span>Cancellation Status </span></td><td><span> : <b> Confirmed</b></span></td>
                                                    </tr> */}
                                                    <tr>
                                                        <td><span>Tentaive Delivery </span></td><td> : <span className="black">within 2 - 3 Days</span></td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row className="order-product payment">
                        <div className="head">Payment Information</div>
                        <Col md={6} className="order-product  pb-3">
                            <div className="head">Shipping Address</div>
                            <div className="shipping-address">
                                <p>{order && order.cust_name}</p>
                                <p>Address : {order && `${order.address.split('^')[0]}, ${order.address.split('^')[1]}, ${order.address.split('^')[2]}, ${order.address.split('^')[3]}`}</p>
                                <p><b><a href="tel:+923001234567">Mobile No: {order && order.cust_phone}</a></b></p>
                            </div>
                        </Col>
                        <Col md={6}>
                            <div className="pb-3">
                                <table>
                                    <tbody>
                                        <tr>
                                            <td><span>Item(s) Subtotal </span></td><td>: <span>  Rs {misc && misc.TotalPrice} </span></td>
                                        </tr>
                                        <tr>
                                            <td><span>Shipping Charges </span></td><td> : <span> Rs {order && order.shipping_charges} </span></td>
                                        </tr>
                                        {/* <tr className="border-bottom">
                                            <td className="pb-3"><span>COD Charges </span></td><td className="pb-3">: <span>  Rs 500 </span></td>
                                        </tr> */}
                                        <tr className="border-bottom">
                                            <td className="pt-3 pb-3"><span><b>Order Total</b> </span></td><td className="pt-3 pb-3"> : <span> <b> Rs {order && order.total_bill} </b> </span></td>
                                        </tr>
                                        <tr className="border-bottom">
                                            <td className="pt-3 pb-3"><span><b>Net Payment</b> </span></td><td className="pt-3 pb-3"> : <span> <b> Rs {order && order.total_bill} </b> </span></td>
                                        </tr>
                                        <tr>
                                            <td className="pt-3"><span>Payment Mode </span></td><td className="pt-3">: <span>  CashOnDelivery </span></td>
                                        </tr>
                                        <tr>
                                            <td><span>Payment Status </span></td><td> : <span> COD-Confirmed </span></td>
                                        </tr>
                                        {/* <tr>
                                            <td><span>Taxes </span></td><td>: <span>  Rs 250 </span></td>
                                        </tr> */}
                                    </tbody>
                                </table>
                            </div>
                        </Col>
                    </Row>
                </div>
                <TrackPopup handleShow={this.handleShowTrack} handleClose={this.handleCloseTrack} show={this.state.trackToggle || false} order={this.state.track_order} detail={this.state.track_detail}/>
            </>
        )
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        order_detail: state.order_detail.order.order_detail,
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        getOrderDetail: Actions.getOrderDetail,
        showResponseMessage: Actions.showResponseMessage,
        updateOrderStatus: Actions.updateOrderStatus
    }, dispatch)
}


export default withReducer('order_detail', reducer)
            (
                connect(mapStateToProps, mapDispatchToProps)(OrderDetails)
            )

