import React, { Component } from "react";
import { useMediaQuery } from 'react-responsive';
import Cookies from "universal-cookie";
import {
  getLocalStorageObject,
  getLocalStorageString,
  productFoundInShortlist, 
  removeProductFromCache,
  removeProductDetailFromCache
} from './Utilities'
import _ from 'lodash'
import {hostUrl} from './store/config'

// ... ACTIONS ... 
import withReducer from "../store/withReducer";
import connect from "react-redux/es/connect/connect";
import reducer from "./store/reducers";
import * as Actions from "./store/actions";
import { bindActionCreators } from "redux";

const Desktop = ({ children }) => {
  const isDesktop = useMediaQuery({ minWidth: 601 })
  return isDesktop ? children : null
}

const Mobile = ({ children }) => {
  const isMobile = useMediaQuery({ maxWidth: 600 })
  return isMobile ? children : null
}

class QtnCount extends Component {
  constructor(props) {
    super(props);
    this.state = {
      value: this.props.cacheid[1],
      total: this.props.cacheid[1] * this.props.pro.Price,
      reRender: false,
      shortlist: false,
    };
  }

  decrease = () => {
    if (this.state.value) {
      this.setState({
        value: this.state.value - 1
      });
    }
  };

  static getDerivedStateFromProps(props, state) {
    return {
      coupon_details: props.coupon_details,
      shipping: props.shipping
    }
  }
 
  increase = () => {
    this.setState({ value: this.state.value + 1 });
  };

  handleValueChange = (value) => {
    this.props.handleValue(value);
  };

  handleTotalchange = (total) => {
    this.props.handleTotal(total);
  };

  getTotalAmount = () => {
    
    let total =  this.props.pro['Sale Price'] ? 
        Math.round(this.props.cacheid[1] * this.props.pro['Sale Price']):
        Math.round(this.props.cacheid[1] * this.props.pro.Price)
    
        // if(this.state.coupon_details && _.find(this.props.pro['is_coupon'].split(','), cop => {return cop === this.state.coupon_details.cop_code}))
        // {
        //   alert('Product Found :: ' + this.props.pro['Name'])
        // }

    if (this.state.coupon_details && _.find(this.props.pro['is_coupon'].split(','), cop => {return cop === this.state.coupon_details.cop_code}))
    {
      console.log('Coupon applied on :: ' + this.props.pro['Name']);
      this.state.coupon_details.cop_type === 'Direct' ?
       total = total - this.state.coupon_details.cop_discount 
       :
       total = total - (this.state.coupon_details.cop_discount/100 * total)
    }

    console.log('sub Total :: ' + total);
    return total;
  }

  render() {
    // const cookies = new Cookies();
    const currentCookies = getLocalStorageObject('shortlist');
    // const currentCache = getLocalStorageString('procache');
    const foundInCookies = productFoundInShortlist(this.props.pro &&  this.props.pro.Id, currentCookies)

    let total = this.getTotalAmount();
    return (
      <>
      <Desktop>
        <tr>
          <td className="img-td">
            <div class="pro-content">
              <img
                src={this.props.pro['Image'] ? `${hostUrl}${(this.props.pro['Image'].split(','))[0]}`:"/assets/images/products/cp1.jpg"}
                alt=""
              />
              <div>
                <a href="#a" className="pro-name">
                  {this.props.pro.Name}
                  {/* {console.log(" pro =>" + currentCache.map((item) => ( item === pro.Id ? pro.Name : " " ) )) } */}
                </a>
                <p className="pro-desc">
                  {(this.props.pro['Description']).replace(/(<([^>]+)>)/ig,"").substring(0, 60)}
                </p>
              </div>
            </div>
          </td>

          <td className="ship-td">
            <p className="pro-delivery">Tentative Delivery</p>
            <p className="pro-days">2 to 3 Business Days</p>
          </td>
          <td className="price-td">

            {this.props.pro['Sale Price'] ? <>
              <div className="main-price"><p class="pro-o-price">Rs {this.props.pro.Price}</p>
              <p className="pro-price">Rs {this.props.pro['Sale Price']}</p>
              <p className="pro-disc">{Math.round(((this.props.pro['Price'] - this.props.pro['Sale Price']) / this.props.pro['Price'] * 100))}% OFF</p>
              <p className="pro-disc-price">Discount Rs {this.props.pro['Price'] - this.props.pro['Sale Price']}</p>
              </div>
              <div className="taxes"><p class="pro-tax">MRP incl. all taxes</p></div>
            </> :
              <p class="pro-price">Rs {this.props.pro.Price}</p>}
          </td>
          <td className="quantity-td">
            <div className="qtn-inc-dec">
              <button
                disabled={this.props.cacheid[1] === 1 ? true : false}
                onClick={e => {
                  if (this.props.cacheid[1]) {
                    this.setState({ value: this.props.cacheid[1] - 1, total: this.props.cacheid[1] * this.props.pro.Price });
                  }
                  this.handleValueChange(this.props.cacheid[1]);
                  const findId = this.props.currentCache.findIndex(
                    x => x[0] === this.props.pro.Id
                  );
                  this.props.currentCache[findId].splice(
                    1,
                    1,
                    this.props.cacheid[1] !== 0 ? this.props.cacheid[1] - 1 : 1
                  );
                  localStorage.setItem(
                    "procache",
                    JSON.stringify(this.props.currentCache)
                  );
                  this.props.saveData(this.props.coupon_details && this.props.coupon_details, this.props.shipping && this.props.shipping);
                  this.props.updateCart();
                }}
                className="minus"
              >
                -
              </button>
              <input
                className="quantity"
                min="1"
                name="quantity"
                value={
                  // this.state.value !== 0
                  //   ? this.props.cacheid[1] || this.state.value
                  //   : 1
                  this.props.cacheid[1]
                }
                onChange={() => console.log("change")}
                type="text"
              />
              <button
                onClick={e => {
                  if((this.state.value && this.state.value) < (this.props.pro && this.props.pro['Stock']))
                  {
                    this.setState({ value: this.props.cacheid[1] + 1, total: this.props.cacheid[1] * this.props.pro.Price });
                    this.handleValueChange(this.props.cacheid[1]);
                    //console.log( "Total value" + this.state.total)
                    //this.handleTotalchange(total);
                    const findId = this.props.currentCache.findIndex(
                      x => x[0] === this.props.pro.Id
                    );
                    this.props.currentCache[findId].splice(
                      1,
                      1,
                      this.props.cacheid[1] !== 0 ? this.props.cacheid[1] + 1 : 1
                    );
                    localStorage.setItem(
                      "procache",
                      JSON.stringify(this.props.currentCache)
                    );
                    this.state.shipping && console.log('Shipping QtnCount :: ' + JSON.stringify(this.state.shipping))
                    this.props.saveData(this.props.coupon_details && this.props.coupon_details, this.state.shipping && this.state.shipping);
                    this.props.updateCart();
                  }
                  else if((this.state.value && this.state.value) == (this.props.pro && this.props.pro['Stock']))
                  {
                    this.props.showCartResponseMessage(`${this.props.pro && this.props.pro['Name']} Stock limit Reached`,'danger')
                  }
                  
                }}
                className="plus"
              >
                +
              </button>
            </div>
            <div className="qtn-action">
              <div
                className={
                  foundInCookies
                    ? "cart-shortlist shortlisted"
                    : "cart-shortlist"
                }
                onClick={() => {
                    this.props.saveProductToShortlist(this.props.pro)
                    this.props.handleShortlist()
                    
                  // cookies.set('shortlist', shortlistArr, { path: '/', expires: d });
                  // console.log(" get shortlist " + cookies.get("shortlist"));
                }}
              />
              <div
                className="cart-remove"
                onClick={() => {
                  removeProductFromCache(this.props.pro.Id);
                  removeProductDetailFromCache(this.props.pro.Id);
                  this.props.saveData(this.props.coupon_details && this.props.coupon_details, this.state.shipping && this.state.shipping);
                }}
              />
            </div>
          </td>
          <td className="total-td text-center">
            <p className="pro-total">
              {total}   
            </p>
            {this.state.coupon_details && _.find(this.props.pro['is_coupon'].split(','), cop => {return cop === this.state.coupon_details.cop_code}) &&
            <span style={{color:'green', fontSize:'11px'}}>coupon applied</span> }
          </td>
        </tr>
        </Desktop>
        <Mobile>
        <tr>
          <td className="img-td">
            <div class="pro-content">
              <img
                src={this.props.pro['Image'] ? `${hostUrl}${(this.props.pro['Image'].split(','))[0]}`:"/assets/images/products/cp1.jpg"}
                alt=""
              />
              <div>
                <a href="#a" className="pro-name">
                  {this.props.pro.Name}
                  {/* {console.log(" pro =>" + currentCache.map((item) => ( item === pro.Id ? pro.Name : " " ) )) } */}
                </a>
                <p className="pro-desc">
                  {(this.props.pro['Description']).replace(/(<([^>]+)>)/ig,"").substring(0, 60)}
                </p>
                  <div className="price-td">
                    {this.props.pro['Sale Price'] ? <>
                    <div className="main-price"><p class="pro-o-price">Rs {this.props.pro.Price}</p>
                      <p className="pro-price">Rs {this.props.pro['Sale Price']}</p>
                      <p className="pro-disc">{Math.round(((this.props.pro['Price'] - this.props.pro['Sale Price']) / this.props.pro['Price'] * 100))}% OFF</p>
                      <p className="pro-disc-price">Discount Rs {this.props.pro['Price'] - this.props.pro['Sale Price']}</p>
                      </div>
                      <div className="taxes"><p class="pro-tax">MRP incl. all taxes</p></div>
                    </> :
                    <p class="pro-price">Rs {this.props.pro.Price}</p>}
                </div>
                  <div className="quantity-td">
                  <div className="qtn-inc-dec">
                    <button
                      disabled={this.props.cacheid[1] === 1 ? true : false}
                      onClick={e => {
                        if (this.props.cacheid[1]) {
                          this.setState({ value: this.props.cacheid[1] - 1, total: this.props.cacheid[1] * this.props.pro.Price });
                        }
                        this.handleValueChange(this.props.cacheid[1]);
                        const findId = this.props.currentCache.findIndex(
                          x => x[0] === this.props.pro.Id
                        );
                        this.props.currentCache[findId].splice(
                          1,
                          1,
                          this.props.cacheid[1] !== 0 ? this.props.cacheid[1] - 1 : 1
                        );
                        localStorage.setItem(
                          "procache",
                          JSON.stringify(this.props.currentCache)
                        );
                        this.props.saveData(this.props.coupon_details && this.props.coupon_details, this.state.shipping && this.state.shipping);
                        this.props.updateCart();
                      }}
                      className="minus icon-minus rm-qty"
                    >
                      
                    </button>
                    <input
                      className="quantity"
                      min="1"
                      name="quantity"
                      value={
                        // this.state.value !== 0
                        //   ? this.props.cacheid[1] || this.state.value
                        //   : 1
                        this.props.cacheid[1]
                      }
                      onChange={() => console.log("change")}
                      type="text"
                    />
                    <button
                      onClick={e => {
                        this.setState({ value: this.props.cacheid[1] + 1, total: this.props.cacheid[1] * this.props.pro.Price });
                        this.handleValueChange(this.props.cacheid[1]);
                        //console.log( "Total value" + this.state.total)
                        //this.handleTotalchange(total);
                        const findId = this.props.currentCache.findIndex(
                          x => x[0] === this.props.pro.Id
                        );
                        this.props.currentCache[findId].splice(
                          1,
                          1,
                          this.props.cacheid[1] !== 0 ? this.props.cacheid[1] + 1 : 1
                        );
                        localStorage.setItem(
                          "procache",
                          JSON.stringify(this.props.currentCache)
                        );
                        this.props.saveData(this.props.coupon_details && this.props.coupon_details, this.state.shipping && this.state.shipping);
                        this.props.updateCart();
                      }}
                      className="plus icon-plus"
                    >
                      
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </td>
          <td className="total-td">
            <p className="pro-total">
              {total}   
            </p>
            {this.state.coupon_details && _.find(this.props.pro['is_coupon'].split(','), cop => {return cop === this.state.coupon_details.cop_code}) &&
            <span style={{color:'green', fontSize:'11px'}}>coupon applied</span> }
          </td>
          <td className="ship-td">
            <p className="pro-delivery">Tentative Delivery: </p>
            <p className="pro-days">2 to 3 Business Days</p>
          </td>
          <td className="quantity-td button">
            <div className="qtn-action">
              <div
                  data-icon-font="#"
                  className="cart-icon-mob cart-remove-mob"
                  onClick={() => {
                    removeProductFromCache(this.props.pro.Id);
                    removeProductDetailFromCache(this.props.pro.Id);
                    this.props.saveData(this.props.coupon_details && this.props.coupon_details, this.state.shipping && this.state.shipping);
                  }}
                >
                <span>REMOVE</span>
              </div>
              <div
                data-icon-font=""
                className={
                  foundInCookies
                    ? "cart-icon-mob cart-shortlist-mob active"
                    : "cart-icon-mob cart-shortlist-mob"
                }
                onClick={() => {
                    this.props.saveProductToShortlist(this.props.pro)
                    this.props.handleShortlist()
                    
                  // cookies.set('shortlist', shortlistArr, { path: '/', expires: d });
                  // console.log(" get shortlist " + cookies.get("shortlist"));
                }}
              > <span>SHORTLIST</span>
              </div>
              </div>
          </td>
        </tr>
        </Mobile>
        </>
      );

  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    coupon_details: state.cartReducer.order.coupon_details,
    shipping: state.cartReducer.shipping.shipping
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators({
    updateCart: Actions.updateCart,
    saveProductToShortlist: Actions.saveProductToShortlist,
    showCartResponseMessage: Actions.showCartResponseMessage
  }, dispatch)
}

export default withReducer('cartReducer', reducer)(connect(mapStateToProps, mapDispatchToProps)(QtnCount))
// export default QtnCount;
