import React, { Component } from "react";
import { Nav, NavDropdown, Tab, Row, Col} from "react-bootstrap";
import CategoryTagOffers from "./CategoryTagOffers"
import catBox from '../catBox.json';
import {getFilterQuery} from './Utilities'
import queryString from 'query-string'

import _ from 'lodash'
import {Link} from 'react-router-dom'
import history from '../history'

const tagFoundInArray = (allTags , tagName) => {

  for (let i = 0 ; i < Object.keys(allTags).length ; i++)
  {
    if (allTags[i].tag === tagName)
      return true;
  }
  return false;

}
export default class CategoryNavItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeTab: 1
    };
  }

  handleDropDown = () => {
    document.getElementById("down-world").style.color = 'green';
  }

  render() {
    
    let category = _.find(catBox.categories, (cat) => {return cat.p_slug === this.props.category.child_slug})

    return (
      // <Nav.Link
      // href={`/parent_cat/${this.props.category.child_slug}`}>
      // </Nav.Link>,
      // <Nav.Link as={Link} to={`/parent_cat/${this.props.category.child_slug}`}>
        
      <div
        className="position-static p-0 dropdown nav-item"
        // menuRole={'ARIA'}
        // onClick={() => {
        //   history.push({
        //     pathname: `/parent_cat/${this.props.category.child_slug}` 
        //   })
        // }}
        // as={this.props.as} to={this.props.to}
        // title={this.props.category.child_name}
        id="basic-nav-dropdown"
      >
        <Link aria-haspopup="true" aria-expanded="false" href="#" class="dropdown-toggle nav-link" role="button" to={`/parent_cat/${this.props.category.child_id}/${this.props.category.child_name}/${this.props.category.child_slug}?age=all&size=all&color=all&price=50,50000`}>{this.props.category.child_name}</Link>
        <div role="ARIA" class="dropdown-menu" aria-labelledby="">
        <Tab.Container
          id="tabs-with-dropdown"
          defaultActiveKey="1"
          activeKey={this.state.activeTab}
        >
          <Row className="clearfix m-0">
            <Col sm={12} className="p-0">
              <Nav className="inner-tab" bsStyle="tabs">
                {this.props.tags.map((tag, tagIndex) => (
                  <Nav.Link
                    key={tag.id+tag.name+tagIndex}
                    className="nav-link-tab"
                    onMouseOver={() => {
                      this.setState({ activeTab: tagIndex + 1 });
                    }}
                    onMouseOut={() => {
                      this.setState({ activeTab: tagIndex + 1 });
                    }}
                    eventKey={tagIndex + 1}
                  >
                    {tag.tag}
                  </Nav.Link>
                ))}
              </Nav>
            </Col>
            <Col sm={12} className="p-0">
              <Tab.Content activeTab={this.state.activeTab} animation>
                {this.props.tags.map((tag, tagIndex) => (
                  <Tab.Pane key={tag.name+tagIndex+tag.id} eventKey={tagIndex + 1}>
                    <Row className="clearfix main">
                      <Col md={this.props.md} sm={12} className=" col-border">
                        <label className="title">Shop by Category</label>
                        { // Rendering Sub-Categories according to the Tag 
                          this.props.subCategories.map(subCat =>
                          subCat.new === true ? (
                            <Nav.Link key={subCat.child_id+subCat.child_name} className="underline" href="#">
                              {subCat.child_name} <span className="new">NEW</span>{" "}
                            </Nav.Link>
                          ) : 
                          
                          ( subCat.AllTags && Object.keys(subCat.AllTags).length >= 1 && tagFoundInArray(subCat.AllTags , tag.tag ) &&
                            <Nav.Link
                              key={subCat.child_id}
                              className={
                                subCat.featured === true
                                  ? "underline featured"
                                  : "underline"
                              }
                              as={Link} to={`/categories/${subCat.child_id}/${tag.tag}/${this.props.category.child_name}/${subCat.child_slug}?age=all&size=all&color=all`}   
                            >
                              {/* {console.log('category :: ' + JSON.stringify(subCat))} */}
                              {subCat.child_name}{" "}
                            </Nav.Link>
                            // <div  onClick={() => {
                            //   history.push({
                            //     pathname: '/categories/'+subCat.child_slug
                            //   })
                            //   this.props.handleNav()
                            //   this.props.handleDropDown(true)
                            // }} 
                            // onMouseOut={()=> {
                            //   this.props.handleDropDown(false)
                            // }}>
                            // {subCat.child_name}{" "}
                            // </div>
                          )
                          )
                        }
                        {/* Shop by AGE */}
                        {category && category.tags.shop_by_age && Object.keys(category.tags.shop_by_age).length > 0 &&
                          <label className="title">Shop by Age</label>}             
                          {category && category.tags.shop_by_age && category.tags.shop_by_age.map((age, key) => {
                            return <Nav.Link
                            key={age + key}
                            className={"underline"}
                            as={Link} to={`/parent_cat/${this.props.category.child_id}/${this.props.category.child_name}/${this.props.category.child_slug}?age=${age}&size=all&color=all&gender=all`}   
                            // `/parent_cat/${category.child_id}/${category.child_name}/${category.child_slug}?age=all&size=all&color=all&price=50,50000`
                          >
                            {age}{" "}
                          </Nav.Link>
                          })
                        }
                        {/* Shop by COLOR */}
                        {category && category.tags.shop_by_color && Object.keys(category.tags.shop_by_color).length > 0 &&
                          <label className="title">Shop by Color</label>}             
                          {category && category.tags.shop_by_color && category.tags.shop_by_color.map((color, key) => {
                            return <Nav.Link
                            key={color + key}
                            className={"underline"}
                            as={Link} to={`/parent_cat/${this.props.category.child_id}/${this.props.category.child_name}/${this.props.category.child_slug}?color=${color}&size=all&age=all&gender=all`}
                          >
                            {color}{" "}
                          </Nav.Link>
                          })
                        }
                      </Col>

                      <Col md={4} sm={12}>
                        <CategoryTagOffers cat_id={this.props.category.child_id} tag={tag}/>
                      </Col>
                    </Row>
                  </Tab.Pane>
                ))}
              </Tab.Content>
            </Col>
          </Row>
        </Tab.Container>
        </div>
      </div>     
      // </Nav.Link>
    );
  }
}
