const options = [
	{ value: 'chocolate', label: 'Chocolate' },
	{ value: 'strawberry', label: 'Strawberry' },
	{ value: 'vanilla', label: 'Vanilla' },
];

const menuHeaderStyle = {
	padding: '8px 12px',
};


class Search extends Component {
	constructor(props) {
		super(props);
		
		this.state = {
			searchData: [],
			selectedTitle: ''
		}
	}
	
	// static getDerivedStateFromProps(props, state) {
	//     return {
	//         searchData : props.searchData
	//     }
	// }    
	
	searchTitles = async productTitle => {
		// console.log('searching for', productTitle)
		let searchTerm = productTitle
		if (!productTitle || productTitle === ' ') {
			let { selectedTitle } = this.state;
			searchTerm = (selectedTitle || {}).label || 'baby';			
		}
		
		const urlRequest = `${hostUrl}/api/customer/search/${searchTerm}`
		
		return fetch(urlRequest)
		.then(response => response.json())
		.then(data => {
			// console.log('Searched Data :: ', (data.data))
			
			let categoryItems = [];
			let productItems = [];
			
			data.data.map((item) => {
				
				let obj = {value:'', label:''}
				
				if(item.Type === 'Category')
				{
					obj.value = `/categories/${item.Id}/All/${item.Parent_Slug}/${item.Slug}?age=all&size=all&color=all&price=50,50000`
					// obj.value = `/categories/${subCat.child_id}/${tag.tag}/${this.props.category.child_name}/${subCat.child_slug}`
					obj.label = item.Name
					categoryItems.push(obj)
				}
				else if(item.Type === 'Parent Category')
				{
					obj.value = `/parent_cat/${item.Id}/${item.Name}/${item.Slug}?age=all&size=all&color=all&price=50,50000`
					obj.label = item.Name
					categoryItems.push(obj)
				}
				else if(item.Type === 'Product')
				{
					obj.value = `/product/${item.Slug}`
					obj.label = item.Name
					productItems.push(obj)
				}
			})
			
			return [
				{
					label: <h6>Category</h6>,
					options: categoryItems,
				},
				{
					label: <h6>Products</h6>,
					options: productItems
				}
			]
			
		})
	}
	
	Menu = props => {
		// console.log(JSON.stringify(props));
		return (
			<Fragment>
				<components.Menu {...'hellooo'}>{props.children}</components.Menu>
			</Fragment>
		);
	};
		
		
	handleRenderItem = (property, value) => {
		// console.log('JD test-', property, value);
		this.setState({ selectedTitle: property })
		
		if(Object.keys(property.value).length > 0)
		{
			history.push({
				pathname: property.value
			})
		}
	}

	componentDidUpdate(p, prevState) {
		// console.log("jd-test", prevState, this.state, this.state.selectedTitle);
	}
		
	render() {
		const { selectedOption } = this.state;
		// console.log('jd-test', this.state.selectedTitle);
		return (				
			<AsyncSelect
				// This is the example that the list was cleared (FIXED)
				cacheOptions
				defaultOptions
				value={this.state.selectedTitle}
				loadOptions={this.searchTitles}
				onChange={(property, value) => {
					this.handleRenderItem(property, value)
				}}
				placeholder={"Search for a Category, Brand or Product"}
				components={this.Menu}
			/>
			
		)
	}
}
		
