import React, { Component } from 'react'
import {Form, Button} from 'react-bootstrap'

import AuthNotification from '../AuthNotification'

// ... REDUX ...
import withReducer from "../../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./../../store/reducers";
import * as Actions from "./../../store/actions";

class LoginForm extends Component {

    constructor(props)
    {
        super(props);

        this.state ={
            remember: false,
            login_msg:null,
            variant: 'black',
            resend: -1
        }
    }

    static getDerivedStateFromProps(props, state) {
        let is_resend = -1
        if(props.is_verified === 0)
        {
            alert('You are not verified yet... want to resend the email ?')
            is_resend = 1
        }
        return {
            login_msg : props.login_msg,
            variant: props.variant,
            is_verified: props.is_verified,
            resend : is_resend
        }
    }

    handleLoginClick = event => {
        let {email, password} = this.form;
        this.props.manualLogin(email.value , password.value, this.state.remember);
    };

    handleChangeRemember = () => {
        this.setState({ remember: !this.state.remember });
    }

    handleResendEmail = () => {
        // we'll resend the email on that email ...
        // alert('resend triggered....')
        let {email} = this.form
        this.props.getResendEmail(email.value)
    }

    render() {
        return (<>
            <AuthNotification />
            <Form className="login"
                ref={form => (this.form = form)}>
                  <Form.Control
                    type="email"
                    autocomplete="off"
                    placeholder="Email id*"
                    maxlength="50"
                    size="25"
                    name="email"
                    autocorrect="off"
                    autocapitalize="off"
                    required
                  />
                  <Form.Control
                    type="password"
                    maxlength="20"
                    name="password"
                    placeholder="Password*"
                  />
                  {/* <p style={{color:`${this.state.variant}`}} >{this.state.login_msg}</p> */}
                  <div>
                      {this.state.resend > -1 && <Button className='btn btn-resend' onClick={this.handleResendEmail}>Resend Email</Button>}
                  </div>
                  <div className="login-check">
                    <div
                      onClick = {this.handleChangeRemember}
                      className={
                        this.state.remember === true
                          ? "bg-check check"
                          : "bg-check"
                      }
                    />
                    <span>Keep me logged In</span>
                  </div>
                  <Button
                    className="btn-submit"
                    onClick={this.handleLoginClick}
                  >
                    LOGIN
                  </Button>
                </Form>

                <div className="forget">
                    <a onClick={() => {
                            this.setState({ forget: !this.state.forget });}}
                        href="#a"
                    >
                        Forget Password?
                    </a>
                </div>

                <div
                    className={
                    this.state.forget === true
                        ? "forget-form active"
                        : "forget-form"}
                >
                                {/* FORGET PASSWORD FORM  */}                  
                    <Form>
                        <div className="title">Forgot Password</div>
                        <span>
                            Please submit the email address associated with your
                            account and we will send you a link to reset password.{" "}
                        </span>
                        <Form.Control
                            type="email"
                            autocomplete="off"
                            placeholder="Email id*"
                            maxlength="50"
                            size="25"
                            name="Email"
                            autocorrect="off"
                            autocapitalize="off"
                            required
                        />
                        <Button className="btn-submit">SUBMIT</Button>
                    </Form>

                </div> 
        </>)
    }
}

const mapStateToProps = (state, ownProps) => {
    return {
        login_msg: state.loginForm.auth.login_msg,
        variant: state.loginForm.auth.variant,
        is_verified: state.loginForm.auth.is_verified,
        user: state.loginForm.auth.user,
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    return bindActionCreators({
        manualLogin: Actions.manualLogin,
        getResendEmail: Actions.getResendEmail
    }, dispatch)
}

export default withReducer('loginForm', reducer)
            (
                connect(mapStateToProps, mapDispatchToProps)(LoginForm)
            )
// export default LoginForm;