import React, { Component } from "react";
import { Navbar } from "react-bootstrap";
import { slide as Menu } from "react-burger-menu";
import Collapse from "rc-collapse";
import "rc-collapse/assets/index.css";
import Ripples from "react-ripples";
import jwtDecode from "jwt-decode";
import { hostUrl } from "./../store/config";

import { Link } from "react-router-dom";

// ... REDUX ...
import withReducer from "../../store/withReducer";
import connect from "react-redux/es/connect/connect";
import { bindActionCreators } from "redux";
import reducer from "./../store/reducers";
import * as Actions from "./../store/actions";

import CartPopup from "../CartPopup";

var Panel = Collapse.Panel;
class MobileHeader extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpen: false,
      itemCount: 0,
      cartToggle: false,
    };
  }

  componentDidMount() {
    this.props.getCartFromCache();
  }

  static getDerivedStateFromProps(props, state) {
    return {
      itemCount: props.itemCount,
      cartItems: props.cartItems,
      procache: props.procache,
    };
  }

  handleShowCart = () => {
    this.setState({ cartToggle: true });
  };

  handleClose = () => {
    this.setState({ cartToggle: false });
  };

  handleStateChange(state) {
    this.setState({ menuOpen: state.isOpen });
  }

  closeMenu() {
    this.setState({ menuOpen: false });
  }

  toggleMenu() {
    this.setState((state) => ({ menuOpen: !state.menuOpen }));
  }

  handleLogout = () => {
    this.closeMenu();
    this.props.Logout();
    this.props.removeUserData();
  };

  render() {
    let categoryData = this.props.categoryData;

    let cust_obj = null;
    let cartItems = null;
    if (this.props.cartItems) cartItems = this.props.cartItems;

    let { itemCount } = this.state;

    if (localStorage.getItem("jwtToken") && localStorage.getItem("_me")) {
      cust_obj = jwtDecode(localStorage.getItem("_me"));
    }

    cust_obj && console.log("Cust obj :: " + JSON.stringify(cust_obj));

    return (
      <div className="mobile-header">
        <Navbar bg="light" expand="lg">
          <div className="d-flex align-items-center">
            <Menu
              className="bm-burger-button"
              noOverlay
              isOpen={this.state.menuOpen}
              onStateChange={(state) => this.handleStateChange(state)}
            >
              {/* <div className="user-profile bg">
                                <Link to={`/myaccount/myprofile`} onClick={this.handleMenuOpen}>
                                    <img width="100%" src={cust_obj.cust_image ? `${hostUrl}/${cust_obj.cust_image}` : "/assets/images/banner_menu_img.jpg"} alt="" />
                                </Link>
                                <div className="user-image" onClick={this.handleLogout} > 
                                    <label>
                                        <label className="icons" data-icon-font="" th-icon=""></label>
                                    </label>
                                    <span className="arrow_link" data-icon-font="n"></span>
                                    <span className="login-name">Logout</span>
                                </div>
                            </div> */}
              <div className="user-profile bg">
                {cust_obj ? (
                  <a href="/myaccount/myprofile" onClick={this.handleMenuOpen}>
                    <img
                      width="100%"
                      src="/assets/images/banner_menu_img.jpg"
                      alt=""
                    />
                  </a>
                ) : (
                  <a href="/login">
                    <img
                      width="100%"
                      src="/assets/images/banner_menu_img.jpg"
                      alt=""
                    />
                  </a>
                )}

                {cust_obj ? (
                  <div className="user-image" onClick={this.handleLogout}>
                    <label>
                      {cust_obj.cust_image ? (
                        <img
                          width="100%"
                          src={`${hostUrl}${cust_obj.cust_image}`}
                          alt=""
                        />
                      ) : (
                        <label
                          className="icons"
                          data-icon-font=""
                          th-icon=""
                        ></label>
                      )}
                    </label>
                    <span className="arrow_link" data-icon-font="n"></span>
                    <span className="login-name">Logout</span>
                  </div>
                ) : (
                  <a
                    href="/login"
                    className="user-image"
                    onClick={() => this.closeMenu()}
                  >
                    <label>
                      <label
                        className="icons"
                        data-icon-font=""
                        th-icon=""
                      ></label>
                    </label>
                    <span className="arrow_link" data-icon-font="n"></span>
                    <span className="login-name">Login / Register</span>
                  </a>
                )}
              </div>

              <div className="quick-icons">
                <div>
                  <Link
                    to={`/myaccount/myprofile`}
                    onClick={this.handleMenuOpen}
                  >
                    <label th-icon="" className="myaccount"></label>
                    <div className="title" onClick={() => this.closeMenu()}>
                      Account
                    </div>
                  </Link>
                </div>
                <div>
                  <Link to={"/myaccount/orderhistory"}>
                    <label th-icon="" data-icon-font="I"></label>
                    <div className="title" onClick={() => this.closeMenu()}>
                      Order History
                    </div>
                  </Link>
                </div>
                <div>
                  <Link to={"/myaccount/trackorder"}>
                    <label th-icon="" className="torder"></label>
                    <div className="title" onClick={() => this.closeMenu()}>
                      Track Order
                    </div>
                  </Link>
                </div>
                {/* <div>
                                    <a href="#a">
                                        <label th-icon="" className="crefund"></label>
                                        <div className="title">Cash Refund</div>
                                    </a>
                                </div> */}
              </div>
              <div className="mobile-nav">
                <Link onClick={() => this.closeMenu()} to={"/"}>
                  <label className="home" th-icon=""></label>
                  <span>Home</span>
                </Link>
                <a href="#a">
                  <label className="offer" th-icon=""></label>
                  <span>Offer Zone</span>
                  <span className="new">New</span>
                </a>
                <Ripples>
                  <Collapse
                    defaultActiveKey={"0"}
                    accordion={false}
                    className="sub-item"
                  >
                    {localStorage.getItem("jwtToken") && (
                      <Panel
                        key={1}
                        isActive={false}
                        header="My Activity"
                        headerClass="my-header-class title"
                      >
                        {/* <a href="#a">
                                                <label className="recently_view_icon" th-icon=""></label>
                                                <span>Recently Viewed</span>
                                            </a> */}
                        <Link
                          to={"/myaccount/myshortlist"}
                          onClick={() => this.closeMenu()}
                        >
                          <label className="mshortlist" th-icon=""></label>
                          <span>My Shortlist</span>
                        </Link>
                        {/* <a href="#a">
                                                <label data-icon-font="P" th-icon=""></label>
                                                <span>Quick Reorder</span>
                                            </a> */}
                      </Panel>
                    )}
                  </Collapse>
                </Ripples>
                <Ripples>
                  <Collapse
                    defaultActiveKey={"1"}
                    accordion={false}
                    className="sub-item"
                  >
                    <Panel
                      key={1}
                      isActive={true}
                      header="Shop by Category"
                      headerClass="my-header-class title"
                    >
                      {this.props.categoryData &&
                        this.props.categoryData.map((category) => {
                          return (
                            <Ripples>
                              <Collapse
                                defaultActiveKey={"0"}
                                accordion={false}
                                className="sub-item inner"
                              >
                                <Panel
                                  key={1}
                                  isActive={true}
                                  header={category.child_name}
                                  headerClass="my-header-class sub-title baby-girl"
                                >
                                  {category.subCategories &&
                                    category.subCategories.map((subCat) => (
                                      <Ripples>
                                        {/* <a className="cat-item" href="#a" style={{textDecoration: 'none'}}>{subCat.child_name}</a> */}
                                        {/* /categories/${subCat.child_id}/${tag.tag}/${this.props.category.child_name}/${subCat.child_slug}?age=all&size=all&color=all */}
                                        <Link
                                          to={`/categories/${subCat.child_id}/All/${category.child_name}/${subCat.child_slug}?age=all&size=all&color=all`}
                                          onClick={() => this.closeMenu()}
                                          className="ml-5"
                                        >
                                          {subCat.child_name}{" "}
                                        </Link>
                                      </Ripples>
                                    ))}
                                </Panel>
                              </Collapse>
                            </Ripples>
                          );
                        })}
                    </Panel>
                  </Collapse>
                </Ripples>
                <a href="#a">
                  <label data-icon-font="9" th-icon=""></label>
                  <span>Contact Us</span>
                </a>
                <a href="#a">
                  <label className="policy" th-icon=""></label>
                  <span>Policies</span>
                </a>
              </div>
            </Menu>
            <Navbar.Brand href="/">
              <img
                className="mobile-logo"
                src="/assets/images/mobile-logo.png"
                alt=""
              />
            </Navbar.Brand>
          </div>
          <div className="right-icons">
            <Link to={`/search`}>
              <label data-icon-font="" th-icon=""></label>
            </Link>
            <a
              data-icon-font="P"
              href="#a"
              th-icon=""
              onClick={this.handleShowCart}
            >
              <span className="prod-count cart-count">{itemCount}</span>
            </a>
          </div>
        </Navbar>
        <CartPopup
          handleShow={this.handleShow}
          handleClose={this.handleClose}
          show={this.state.cartToggle}
        />
      </div>
    );
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    itemCount: Math.max(
      state.mobHeader.cart.itemCount ? state.mobHeader.cart.itemCount : 0,
      state.mobHeader.auth.itemCount ? state.mobHeader.auth.itemCount : 0
    ),
    cartItems: state.mobHeader.cart.cartItems,
    procache: state.mobHeader.cart.procache,
  };
};

const mapDispatchToProps = (dispatch, ownProps) => {
  return bindActionCreators(
    {
      Logout: Actions.Logout,
      removeUserData: Actions.removeUserData,
      getCartFromCache: Actions.getCartFromCache,
    },
    dispatch
  );
};

export default withReducer(
  "mobHeader",
  reducer
)(connect(mapStateToProps, mapDispatchToProps)(MobileHeader));

// export default MobileHeader;
