import React, { Component } from 'react';
import { Nav } from "react-bootstrap";
import {hostUrl} from './store/config'


class CategoryTagOffers extends Component {
    constructor(props) {
        super(props);
        this.state = {
            offerData: null
        }
    }

    componentDidMount() {
        fetch(`${hostUrl}/api/admin/offers/specific?category_id=${this.props.cat_id}&tag_id=${this.props.tag.id}`)
        .then (response => response.json())
        .then (data => {
            if (data.status === true)
            {
                this.setState({offerData: data.data})
            }
        })
    }
    
    render() {
        return (
            this.state.offerData && <>
                <Nav.Link href={this.state.offerData.link}>
                    <img
                    src={`${hostUrl}${this.state.offerData.image}`}
                    alt={`OfferFor${this.props.tag}`}
                    />
                </Nav.Link>
            </>
        )
    }
}

export default CategoryTagOffers
